.title {
    &__big {
        font-family: Montserrat, sans-serif;
        font-style: normal;
        font-weight: 800;
        font-size: 36px;
        line-height: 30px;
        text-align: center;
        letter-spacing: 1px;
        text-transform: uppercase;

        color: #2332B0;
        display: block;
    }
}

@media (min-width: 768px) {
    .title {
        &__big {
            font-weight: 800;
            font-size: 72px;
            line-height: 75px;
            text-align: left;
        }
    }
}