.sorting-section {
  padding: 0 10px;

  & .pagination {
    display: none;
  }

  &__title {
    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 40px;
    text-align: center;
    color: #2230a8;
  }

  &__sort-by {
    @include list-reset;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin: 0 7px;
    margin-bottom: 16px;
  }

  &__item {
    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 25px;
    color: #262626;
    opacity: 0.65;
    border-bottom: 1px dashed rgba(38, 38, 38, 0.65);

    &--current {
      padding-left: 5px;
      padding-right: 14px;
      font-family: Montserrat, sans-serif;
      font-style: normal;
      font-weight: 500;
      font-size: 12px;
      line-height: 25px;
      opacity: 1;
      color: #2230a8;
      border-bottom: 1px dashed #2230a8;
      position: relative;

      &::after {
        position: absolute;
        content: "";
        width: 10px;
        height: 12px;
        right: 0;
        top: 7px;
        background-image: url("data:image/svg+xml,%3Csvg width='10' height='12' viewBox='0 0 10 12' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M9.08069 6.33258C9.38743 6.59388 9.41931 7.0487 9.15189 7.34843L5.22208 11.7531C5.08214 11.91 4.87961 12 4.66667 12C4.45372 12 4.25119 11.91 4.11126 11.7531L0.181441 7.34843C-0.0859772 7.0487 -0.0540977 6.59388 0.252647 6.33258C0.559392 6.07127 1.02484 6.10242 1.29226 6.40216L3.92983 9.35845L3.92983 0.72C3.92983 0.322355 4.25972 -1.73816e-08 4.66667 0C5.07361 1.73816e-08 5.40351 0.322355 5.40351 0.72L5.40351 9.35845L8.04107 6.40216C8.30849 6.10242 8.77394 6.07127 9.08069 6.33258Z' fill='%232230A8'/%3E%3C/svg%3E%0A");
      }

      &--up::after {
        transform: rotate(180deg);
      }
    }
  }

  &__cards-range {
    display: none;
  }

  &__cards-amount {
    display: flex;
    width: 175px;
    margin: 0 auto;

    & button {
      font-family: Montserrat, sans-serif;
      font-style: normal;
      font-weight: normal;
      font-size: 12px;
      line-height: 25px;
      border: none;
      background-color: transparent;
      color: #2230a8;
    }
  }

  &__amount-list {
    @include list-reset;
    display: flex;
    width: 100px;
    height: 30px;
    background: rgba(255, 255, 255, 0.55);
    backdrop-filter: blur(25px);
    /* Note: backdrop-filter has minimal browser support */
    border: 2px solid #fff;
    border-radius: 50px;
    justify-content: space-between;
    padding: 0 6px;
  }

  &__amount-item {
    position: relative;
    width: auto;
    min-width: 15px;
    height: 26px;

    & label {
      font-family: Montserrat, sans-serif;
      font-style: normal;
      font-weight: normal;
      font-size: 12px;
      line-height: 25px;
      text-align: center;

      color: #2230a8;
    }

    & input:checked~label {
      position: absolute;
      height: 30px;
      width: 30px;
      padding-top: 2px;
      border: 2px solid #fff;
      border-radius: 50%;
      top: -2px;
      left: -7px;
    }
  }
}

@media (min-width: 768px) {
  .sorting-section {
    &__wrapper {
      display: flex;
      flex-wrap: wrap;
      width: 85%;
      margin: 0 auto;
      align-items: center;
    }

    &__title {
      margin-right: 50px;
      font-weight: 600;
      font-size: 18px;
      line-height: 40px;
    }

    &__sort-by {
      max-width: 65%;
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-top: 10px;
    }

    &__item {
      font-weight: 500;
      font-size: 16px;
      line-height: 25px;
    }

    &__cards-amount {
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: space-evenly;

      & button {
        font-family: Montserrat, sans-serif;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 25px;

        color: #2230a8;
      }
    }

    &__cards-range {
      display: block;
      font-family: Montserrat, sans-serif;
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      line-height: 25px;
      color: #262626;
    }

    &__amount-list {
      width: 140px;
      height: 40px;
      align-items: center;
      padding: 0 10px;
    }

    &__amount-item {
      & input:checked~label {
        padding-top: 7px;
        top: -7px;
        left: -12px;
        height: 40px;
        width: 40px;
      }
    }
  }
}

@media (min-width: 1024px) {
  .sorting-section {
    padding-bottom: 35px;

    &__wrapper {
      width: auto;
      margin: 0 18px;
    }

    &__title {
      font-weight: 600;
      font-size: 24px;
      margin-right: 22px;
      margin-left: auto;
    }

    &__sort-by {
      margin-right: auto;
      width: auto;
    }

    &__item {
      font-weight: 500;
      font-size: 18px;

      &:not(:last-child) {
        margin-right: 40px;
      }
    }

    &__cards-amount {
      width: 60%;
      font-size: 16px;
    }

    &__amount-item {
      & label {
        font-size: 16px;
      }
    }

    & .pagination {
      display: flex;
      width: 40%;
      justify-content: flex-start;
    }

    &__cards-range {
      margin-right: 50px;
      font-size: 16px;
    }
  }

  .catalog-section .sorting-section {
    grid-row: 4;
    grid-column: 2/5;

    &__title {
      margin-left: 0;
      margin-right: 0;
      font-weight: 500;
      font-size: 18px;
    }

    &__sort-by {
      margin: 0;
      margin-left: auto;
    }

    &__item a {
      font-weight: 500;
      font-size: 18px;
    }

    &__cards-amount {
      width: 100%;
      justify-content: start;
      margin-bottom: 57px;
    }

    & .pagination {
      order: 1;
      width: 100%;
      display: flex;
      justify-content: center;
    }
  }
}

@media (min-width: 1207px) {
  .sorting-section {
    &__wrapper {
      max-width: 1146px;
      margin: 0 auto;
    }

    & .pagination {
      display: flex;
      width: 50%;
      justify-content: flex-start;
    }

    &__cards-amount {
      width: 50%;
      font-size: 16px;
    }
  }
}

@media (min-width: 1200px) {
  .catalog-section .sorting-section {
    &__cards-amount {
      width: 60%;
      margin: 0;
    }

    & .pagination {
      margin: 0;
      width: 30%;
      margin-left: auto;
    }

    &__cards-range {
      margin-right: 40px;
    }
  }

}