.tabs-section {
  padding-top: 50px;
  padding-bottom: 50px;

  &__goods-list {
    display: flex;

    &--hidden {
      display: none;
    }
  }

  &__tabs-list {
    @include list-reset;
    display: flex;

    &--open-mobile {
      display: flex;
      flex-direction: column;

      & .tabs-section__item {
        display: block;
      }
    }
  }

  &__item {
    display: none;
    padding-right: 15px;
    padding-left: 5px;
    margin: 0 auto;
    border-bottom: 1px solid #2230a8;

    &--current {
      display: block;
      background-image: url("data:image/svg+xml,%3Csvg width='10' height='10' viewBox='0 0 10 10' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M5.85749 6.90483C5.46909 7.55217 4.53091 7.55217 4.14251 6.90483L2.1587 3.59848C1.75878 2.93195 2.2389 2.08398 3.01619 2.08398L6.98381 2.08398C7.7611 2.08398 8.24122 2.93196 7.8413 3.59848L5.85749 6.90483Z' fill='%232230A8'/%3E%3C/svg%3E%0A");
      background-repeat: no-repeat;
      background-position: right;
    }
  }

  &__more-link {
    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: 300;
    font-size: 14px;
    line-height: 25px;
    text-align: center;
    display: block;
    margin: 0 auto;
    color: #262626;
  }
}

@media (min-width: 768px) {
  .tabs-section {
    padding-top: 0;
    background-color: #fff;

    &__tabs-list {
      margin: 0 20px;
    }

    &__item {
      padding-bottom: 14px;
      background-image: none;
      display: block;
      font-family: Montserrat, sans-serif;
      width: 33.333%;
      font-style: normal;
      font-weight: 500;
      font-size: 16px;
      line-height: 25px;
      text-align: center;
      color: #262626;
      border-bottom: 2px solid rgba(38, 38, 38, 0.15);

      &--current {
        border-bottom: 2px solid #2230a8;
      }
    }

    &__more-link {
      font-size: 16px;
    }
  }
}

@media (min-width: 1024px) {
  .tabs-section {
    &__tabs-list {
      max-width: 820px;
      margin: 0 auto;
    }
  }
}

@media (min-width: 1200px) {
  .tabs-section {
    margin-top: -1px;

    &__wrapper {
      max-width: 1146px;
      margin: 0 auto;
    }

    &__tabs-list {
      margin: 0;
      margin-left: 20px;
    }
  }
}