.breadcrumbs {
  @include list-reset;
  padding-left: 10px;
  display: flex;
  font-family: Montserrat, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 25px;
  display: flex;
  align-items: center;
  color: #262626;
  margin-bottom: 30px;
  margin-top: 15px;

  &__item {
    margin-right: 10px;
    position: relative;

    &:not(:last-child)::after {
      position: absolute;
      content: "/";
      right: -7px;
    }

    &:last-child {
      font-weight: 600;
    }
  }
}

@media (min-width: 768px) {
  .breadcrumbs {
    font-size: 14px;
    line-height: 25px;
    align-items: center;
    justify-content: center;
  }
}

@media (min-width: 1024px) {
  .breadcrumbs {
    font-size: 16px;
    line-height: 25px;
    text-align: left;
    justify-content: flex-start;
  }

  .page-main--about-us .breadcrumbs {
    margin-left: 10%;
    margin-right: 10%;
  }
}

@media (min-width: 1200px) {
  .page-main--about-us .breadcrumbs {
    width: 1146px;
    margin: 0 auto;
    margin-bottom: 20px;

    & a {
      font-size: 16px;
      line-height: 25px;
    }
  }
}