.text-block {
  &__wrapper {
    background-image: url("../img/rectangle-265.jpg");
    background-repeat: no-repeat;
    padding: 10px;
    padding-top: 300px;
  }
  &__title {
    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    color: #2230a8;
  }
  &__text {
    font-size: 14px;
    line-height: 25px;
  }
}

@media (min-width: 768px) {
  .text-block {
    &__wrapper {
      max-width: 540px;
      margin: 0 auto;
      padding-top: 70px;
      padding-bottom: 416px;
      background-image: url("../img/rectangle-262.png");
      background-position: bottom;
    }
  }
}

@media (min-width: 1024px) {
  .text-block {
    &__wrapper {
      width: 820px;
      margin: 0 auto;
      max-width: unset;
    }
    &__title {
      font-family: Montserrat, sans-serif;
      font-style: normal;
      font-weight: 600;
      font-size: 24px;
      line-height: 40px;
      display: flex;
      align-items: center;
      color: #2230a8;
    }
    &__text {
      font-size: 16px;
    }
  }
}
