.contact-section {
  width: 100%;

  &--white {
    background-color: #fff;
  }

  &__title {
    padding: 0 10%;
    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 25px;
    text-align: center;
    color: #2230a8;
  }

  &__form {
    display: block;
    width: 93.75%;
    margin: 0 auto;
  }

  &__input {
    display: block;
    width: 100%;
    background: rgba(255, 255, 255, 0.55);
    border: 2px solid #2230a8;
    box-sizing: border-box;
    backdrop-filter: blur(25px);
    border-radius: 50px;
    margin-bottom: 20px;
    padding: 10px;
    padding-left: 33px;

    &--textarea {
      min-height: 100px;
      height: 100px;
      border-radius: 20px;
    }
  }

  &__submit-button {
    background: #ff4557;
    border-radius: 30px;
    border: none;
    display: block;
    width: 100%;
    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 45px;
    text-align: center;
    letter-spacing: 0.05em;
    text-transform: uppercase;

    color: #ffffff;
  }

  &__text {
    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 15px;
    text-align: center;
    color: #2230a8;
    opacity: 0.5;
    margin-bottom: 20px;
  }

  &__subtext {
    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 45px;
    text-align: center;
    letter-spacing: 0.05em;
    color: #2230a8;
  }
}

@media (min-width: 768px) {
  .contact-section {
    padding-bottom: 88px;

    &__input-wrapper {
      display: flex;
      flex-wrap: wrap;
      flex-direction: row;
      justify-content: space-between;
    }

    &__input {
      max-width: 280px;

      &:nth-child(odd) {
        width: 206px;
      }

      &--textarea {
        max-width: 100%;
        width: 100%;
        height: 39px;
        min-height: 39px;
      }
    }

    &__title {
      font-family: Montserrat, sans-serif;
      font-style: normal;
      font-weight: 800;
      font-size: 24px;
      line-height: 45px;
      /* or 187% */

      text-align: center;

      /*  */

      color: #2230a8;
    }

    &__submit-button {
      width: 280px;
      margin: 0 auto;
    }

    &__text {
      & br:not(:nth-child(2)) {
        display: none;
      }
    }
  }
}

@media (min-width: 1024px) {
  .contact-section {
    &__input-wrapper {
      width: 820px;
      margin: 0 auto;
    }

    &__input {
      max-width: 316px;

      &:nth-child(odd) {
        width: 232px;
      }

      &--textarea {
        max-width: 100%;
        width: 100%;
        height: 39px;
        min-height: 39px;
      }
    }
  }
}

@media (min-width: 1200px) {
  .contact-section {
    &__wrapper {
      max-width: 1146px;
      margin: 0 auto;
    }

    &__form {
      width: auto;
    }

    &__input-wrapper {
      width: 1146px;
      max-width: 1146px;
      margin: 0 auto;
    }

    &__input {
      width: 264px;
    }
  }
}