.discount-section {
  &__wrapper {
    width: 100%;
  }

  &__form {
    margin-bottom: 27px;
  }

  &__form-title {
    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 30px;
    display: flex;
    align-items: center;
    text-align: center;
    letter-spacing: 0.03em;
    color: #2230a8;
  }

  &__form-text {
    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 25px;
    /* or 179% */
    text-align: center;

    /*  */

    color: #262626;
  }

  &__input {
    background: rgba(255, 255, 255, 0.55);
    border: 1px solid #2230a8;
    box-sizing: border-box;
    backdrop-filter: blur(25px);
    display: block;
    width: 93.75%;
    height: 32px;
    text-align: center;
    border-radius: 50px;
    margin: 0 auto;
    margin-bottom: 20px;
  }

  &__button {
    width: 50%;
    text-align: center;
    display: block;
    margin: 0 auto;
    min-height: 40px;
    background: #2230a8;
    border: none;
    border-radius: 50px;
    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    /* or 321% */
    text-align: center;
    letter-spacing: 0.05em;
    text-transform: uppercase;

    color: #ffffff;
  }

  &__img-wrapper {
    display: none;
  }

  &__countdown {
    background: #ffffff;
    border-radius: 38px 38px 38px 0px;
    width: 93.75%;
    margin: 0 auto;
    padding-top: 17px;
    padding-bottom: 17px;

    & input {
      font-family: Montserrat, sans-serif;
      font-style: normal;
      font-weight: 800;
      font-size: 36px;
      line-height: 45px;
      width: 100%;
      text-align: center;
      color: #ff4557;
      background-color: transparent;
      border: none;
    }
  }

  &__countdown-text {
    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 10px;
    line-height: 25px;
    text-align: center;

    margin: 0;

    color: #262626;
  }
}

@media (min-width: 768px) {
  .discount-section {
    margin-top: -50px;
    padding-top: 105px;
    background: linear-gradient(180deg,
        rgba(255, 255, 255, 0) 0%,
        #ffffff 30.07%);

    &__wrapper {
      padding: 0 20px;
      padding-bottom: 75px;
    }

    &__form-wrapper {
      background: #46bfff;
      border-radius: 38px;
      display: flex;
      flex-direction: column;
      flex-wrap: wrap;
      height: 320px;
      padding-left: 57px;
      padding-top: 15px;
    }

    &__form-title {
      text-align: left;
      font-weight: bold;
      font-size: 36px;
      line-height: 40px;
      color: #fff;
      margin: 0;
      width: 333px;
      margin-top: 35px;
    }

    &__form-text {
      text-align: left;
      font-size: 14px;
      line-height: 25px;
    }

    &__controls {
      display: flex;
    }

    &__input {
      margin: 0;
      margin-right: -10px;
      width: 224px;
    }

    &__button {
      line-height: normal;
      font-family: Montserrat, sans-serif;
      font-style: normal;
      font-weight: 600;
      font-size: 14px;
      padding: 0 20px;
      text-align: center;
      letter-spacing: 0.05em;
      text-transform: uppercase;
      min-height: unset;
      display: inline;
      height: auto;
      color: #ffffff;
    }

    &__img-wrapper {
      display: block;
      width: 280px;
      height: 183px;
      overflow: hidden;
      margin-bottom: 20px;
      margin-right: 16px;
      margin-left: auto;
      z-index: 2;
      border-radius: 34px;
    }

    &__countdown {
      width: auto;
      max-width: 300px;
      height: 102px;
      margin-left: auto;
      margin-right: 0;
      background: #ffffff;
      border-radius: 38px 0 38px 0px;
      position: relative;

      & input {
        color: #2230a8;
      }

      &::after {
        position: absolute;
        content: "";
        background: radial-gradient(circle at 100% 0, #46bfff 70%, white 70%);
        width: 50px;
        height: 50px;
        transform: rotate(-90deg) translate(36px, -50px);
      }

      &::before {
        position: absolute;
        content: "";
        background: radial-gradient(circle at 100% 0, #46bfff 70%, white 70%);
        width: 50px;
        height: 50px;
        transform: rotate(270deg) translate(66px, 251px);
      }
    }
  }
}

@media (min-width: 1024px) {
  .discount-section {
    &__form-wrapper {
      min-height: 431px;
    }

    &__form-title {
      font-weight: bold;
      font-size: 48px;
      line-height: 55px;
      width: 451px;
    }

    &__form-text {
      font-size: 16px;
      line-height: 25px;
    }

    &__input {
      width: 210px;
      height: 43px;
    }

    &__button {
      width: 230px;
      height: 43px;
    }

    &__controls {
      display: flex;
      width: 429px;
    }

    &__img-wrapper {
      display: block;
      width: 380px;
      height: 248px;
      overflow: hidden;
      margin-bottom: 20px;
      margin-right: 16px;
      margin-left: auto;
      z-index: 2;
    }

    &__countdown {
      max-width: 400px;
      height: 140px;
      margin-top: 8px;
      transform: translate(0.5px);
      box-shadow: 4px 4px 8px 1px #ffffff;

      & input {
        font-weight: 800;
        font-size: 64px;
      }

      &::after {
        transform: rotate(-90deg) translate(31px, -49px);
      }

      &::before {
        transform: rotate(270deg) translate(66px, 350px);
      }
    }

    &__countdown-text {
      font-size: 14px;
    }
  }
}

@media (min-width: 1200px) {
  .discount-section {
    &__wrapper {
      max-width: 1146px;
      margin: 0 auto;
    }
  }
}