.banner-section {
  &__item {
    &:not(:first-child) {
      display: none;
    }

    width: 93.75%;
    margin: 0 auto;
    border: 2px solid #fff;
    background-image: linear-gradient(180deg,
      rgba(255, 255, 255, 0.3) 0%,
      rgba(255, 255, 255, 0) 100%);
    backdrop-filter: blur(15px);
    background-repeat: no-repeat;
    background-position: center;
    border-radius: 30px;
    padding-top: 45px;
    padding-bottom: 30px;
    display: flex;
    flex-direction: column;
  }

  &__title {
    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 24px;
    text-align: center;
    color: #2230a8;
    margin-right: 25px;
    margin-left: 25px;
  }

  &__text {
    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 20px;
    text-align: center;
    color: #262626;
    padding: 0 30px;
  }

  &__img-wrapper {
    width: 169px;
    height: 168px;
    overflow: hidden;
    margin: 0 auto;

    &--shadow {
      display: none;
    }
  }

  &__more {
    background-image: url("data:image/svg+xml,%3Csvg width='26' height='8' viewBox='0 0 26 8' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M25.3536 4.35355C25.5488 4.15829 25.5488 3.84171 25.3536 3.64645L22.1716 0.464466C21.9763 0.269204 21.6597 0.269204 21.4645 0.464466C21.2692 0.659728 21.2692 0.976311 21.4645 1.17157L24.2929 4L21.4645 6.82843C21.2692 7.02369 21.2692 7.34027 21.4645 7.53553C21.6597 7.7308 21.9763 7.7308 22.1716 7.53553L25.3536 4.35355ZM0 4.5H25V3.5H0V4.5Z' fill='black'/%3E%3C/svg%3E%0A");
    background-repeat: no-repeat;
    background-position: right;
    padding-right: 40px;
    margin: 0 auto;
  }
}

@media (min-width: 768px) {
  .banner-section {
    &__item {
      overflow: hidden;
      text-align: left;
      background-image: linear-gradient(180deg,
          rgba(255, 255, 255, 0.3) 0%,
          rgba(255, 255, 255, 0) 100%);
      background-position: center;
      position: relative;
      display: flex;
      flex-direction: column;
      flex-wrap: wrap;
      max-height: 267px;
      padding: 0;
      padding-bottom: 20px;
      padding-left: 54px;
    }

    &__img-wrapper {
      width: 227px;
      height: 225px;
      margin: 0;
      order: 2;
      margin-left: -54px;
      margin-top: 20px;

      &--shadow {
        display: block;
        position: absolute;
        z-index: -2;
        right: 0;
        filter: blur(10px);
      }
    }

    &__title {
      text-align: left;
      font-weight: bold;
      font-size: 24px;
      line-height: 30px;
      max-width: 356px;
      margin: 0;
      margin-top: 44px;
      margin-bottom: 23px;
    }

    &__text {
      text-align: left;
      font-weight: 600;
      font-size: 14px;
      line-height: 25px;
      padding: 0;
      margin: 0;
      margin-bottom: 30px;
    }

    &__more {
      font-size: 14px;
      line-height: 25px;
      margin: 0;
      display: inline;
      align-self: start;
    }
  }
}

@media (min-width: 1024px) {
  .banner-section {
    padding-bottom: 70px;

    &__container {
      width: 988px;
      margin: 0 auto;
      overflow: hidden;
      position: relative;
    }

    &__wrapper {
      display: flex;
    }

    &__item {
      &:not(:first-child) {
        display: flex;
      }

      width: 988px;
      min-width: 988px;
      background: #50d0bc;
      border-radius: 50px;
      background-image: url("../img/fon.png");
      background-position: 100%;
      background-repeat: no-repeat;
      border: none;
      max-height: 317px;
      padding-left: 84px;
      padding-top: 20px;
      padding-bottom: 20px;
    }

    &__img-wrapper {
      width: 279px;
      height: 277px;
      margin-top: auto;

      &--shadow {
        display: none;
      }
    }

    &__title {
      font-weight: bold;
      font-size: 24px;
      line-height: 30px;
      max-width: 409px;
      margin-top: 46px;
      margin-bottom: 20px;
    }

    &__text {
      font-weight: normal;
      font-size: 24px;
      line-height: 30px;
      max-width: 290px;
    }

    &__more {
      font-size: 14px;
      line-height: 25px;
      margin: 0;
      display: inline;
      align-self: start;
    }

    &__more {
      display: none;
    }

    &__swiper-pagination {
      width: 200px;
      height: 10px;
      position: absolute;
      top: 253px;
      left: 88px;
      z-index: 10;
      display: flex;
      justify-content: space-between;

      & .swiper-pagination-bullet {
        background: rgba(255, 255, 255, 0.55);
        border-radius: 5.5px;
        width: 50px;
        height: 10px;
      }

      & .swiper-pagination-bullet-active {
        background: #2230a8;

        border-radius: 5.5px;
      }
    }
  }
}

