.articles-section {
  &__title {
    margin: 0;
    margin-top: 50px;
    margin-bottom: 17px;
    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 45px;
    text-align: center;
    color: #28348a;
  }

  &__link {
    background: #2230a8;
    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 45px;
    text-align: center;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    display: block;
    width: 60%;
    margin: 0 auto;
    border-radius: 50px;
    color: #ffffff;
  }
}

@media (min-width: 768px) {
  .articles-section {
    &__title {
      font-family: Montserrat, sans-serif;
      font-style: normal;
      font-weight: 800;
      font-size: 24px;
      line-height: 45px;
      text-align: center;
      color: #28348a;
    }

    &__link {
      width: 207px;
    }
  }
}

@media (min-width: 1200px) {
  .articles-section {
    &__wrapper {
      padding-top: 150px;
      max-width: 1146px;
      margin: 0 auto;
    }
    &__title {
      margin-top: 0;
    }
  }
}
