.nav-sublist {
    @include list-reset;
    display: none;
    width: 100%;
    padding: 10px;
    padding-right: 0;
    padding-left: 25px;
    max-width: 230px;

    &--catalog {
        border: 1px solid #FFFFFF;
        border-left: none;
        border-right: none;
    }

    &__item {
        display: flex;
        align-items: center;
        flex-wrap: wrap;

        &:focus-within>svg,
        &:hover>svg {
            stroke: #FFB054;
        }

        &--current>a {
            position: relative;
            padding-left: 24px;

            &::before {
                position: absolute;
                width: 16px;
                height: 1px;
                content: "";
                top: 50%;
                left: 0;
                background-color: #fff;
            }
        }

        &>a {
            margin-left: 12px;
            font-family: Montserrat, sans-serif;
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            line-height: 45px;
            display: flex;
            align-items: center;
            letter-spacing: 0.05em;
            text-transform: capitalize;
            color: #FFFFFF;
            position: relative;
        }

        &--arrow>a {
            &::after {
                position: absolute;
                content: '';
                width: 10px;
                height: 10px;
                background-image: url("data:image/svg+xml,%3Csvg width='10' height='10' viewBox='0 0 10 10' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M6.90385 4.14251C7.55119 4.53091 7.55119 5.46909 6.90385 5.85749L3.5975 7.8413C2.93098 8.24122 2.08301 7.7611 2.08301 6.98381L2.08301 3.01619C2.08301 2.2389 2.93098 1.75878 3.5975 2.1587L6.90385 4.14251Z' fill='white'/%3E%3C/svg%3E%0A");
                right: -20px;
                top: 17px;
            }

            &:hover {
                &::after {
                    background-image: url("data:image/svg+xml,%3Csvg width='10' height='10' viewBox='0 0 10 10' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M6.90385 4.14251C7.55119 4.53091 7.55119 5.46909 6.90385 5.85749L3.5975 7.8413C2.93098 8.24122 2.08301 7.7611 2.08301 6.98381L2.08301 3.01619C2.08301 2.2389 2.93098 1.75878 3.5975 2.1587L6.90385 4.14251Z' fill='%23FFB054'/%3E%3C/svg%3E%0A");
                }
            }
        }

        &--mobile-sublist {
            &>svg {
                stroke: #FFB054;
            }

            &>a {
                font-weight: bold;
                font-size: 14px;
                line-height: 45px;
                display: flex;
                align-items: center;
                letter-spacing: 0.05em;
                text-transform: capitalize;
                color: #FFB054;

                &::after {
                    background-image: url("data:image/svg+xml,%3Csvg width='10' height='10' viewBox='0 0 10 10' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M5.85749 6.90483C5.46909 7.55217 4.53091 7.55217 4.14251 6.90483L2.1587 3.59848C1.75878 2.93195 2.2389 2.08398 3.01619 2.08398L6.98381 2.08398C7.7611 2.08398 8.24122 2.93196 7.8413 3.59848L5.85749 6.90483Z' fill='%23FFB054'/%3E%3C/svg%3E%0A");
                }
            }

            & .nav-sublist {
                display: block;
            }
        }
    }
}