.goods-card {
    &__img-wrapper {
        width: 93.75%;
        margin: 0 auto;
        position: relative;
        padding-bottom: 42px;
        overflow: hidden;
    }

    &__img-list {
        @include list-reset;
        display: flex;
    }

    &__img-item {
        min-width: 300px;
        width: 300px;
        height: 374px;
        border-radius: 30px;
        overflow: hidden;
    }

    &__link {
        background-image: url("data:image/svg+xml,%3Csvg width='240' height='41' viewBox='0 0 240 41' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cg filter='url(%23filter0_b)'%3E%3Crect y='0.101562' width='240' height='40' rx='20' fill='white' fill-opacity='0.2'/%3E%3Crect x='1' y='1.10156' width='238' height='38' rx='19' stroke='url(%23paint0_linear)' stroke-width='2'/%3E%3C/g%3E%3Cdefs%3E%3Cfilter id='filter0_b' x='-25' y='-24.8984' width='290' height='90' filterUnits='userSpaceOnUse' color-interpolation-filters='sRGB'%3E%3CfeFlood flood-opacity='0' result='BackgroundImageFix'/%3E%3CfeGaussianBlur in='BackgroundImage' stdDeviation='12.5'/%3E%3CfeComposite in2='SourceAlpha' operator='in' result='effect1_backgroundBlur'/%3E%3CfeBlend mode='normal' in='SourceGraphic' in2='effect1_backgroundBlur' result='shape'/%3E%3C/filter%3E%3ClinearGradient id='paint0_linear' x1='87.6404' y1='47.4349' x2='62.9492' y2='-13.441' gradientUnits='userSpaceOnUse'%3E%3Cstop stop-color='white'/%3E%3Cstop offset='0.953057' stop-color='white' stop-opacity='0'/%3E%3C/linearGradient%3E%3C/defs%3E%3C/svg%3E%0A");
        background-repeat: no-repeat;
        background-size: cover;
        background-position: right;
        border-radius: 50px;
        font-family: Montserrat, sans-serif;
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 20px;
        text-align: center;
        color: #2230A8;
        display: block;
        margin: 0 auto;
        width: 240px;
        padding-top: 14px;
        padding-bottom: 14px;
        margin-bottom: 50px;
    }

    &__swiper-pagination {
        position: absolute;
        bottom: 20px;
        z-index: 10;
        width: auto;
        display: flex;
        align-items: center;
        left: 50%;

        transform: translate(-50%);

        & .swiper-pagination-bullet {
            width: 8px;
            height: 8px;
            border: 2px solid #2230A8;
            border-radius: 50%;

            &:not(:last-child) {
                margin-right: 6px;
            }

            &-active {
                border: none;
                width: 13px;
                height: 13px;
                background-color: #2230A8;
            }
        }
    }
}


@media (min-width: 768px) {
    .goods-card {
        &__img-wrapper {
            width: 100%;
        }

        &__img-item {
            min-width: 354px;
            width: 354px;
            height: 441px;
            border-radius: 30px;
            overflow: hidden;
        }

        &__link {
            &:hover {
                background: #FFB054;
            }
        }
    }
}

@media (min-width: 1024px) {
    .goods-card {

        &__img-item {
            min-width: 316px;
            width: 316px;
            height: 394px;
            border-radius: 30px;
            overflow: hidden;
        }
    }
}