.page-main {
  background-image: linear-gradient(to right, #a8edea 22.23%, #fed6e3 83.12%);
  flex-direction: column;
  display: flex;

  &--catalog-page {
    padding-bottom: 90px;
  }

  &--about-us {
    & .page-main__section {
      &--hit {
        order: unset;
      }
    }
  }

  &__section {
    width: 100%;

    &--welcome {
      order: 1;
    }

    &--holyday {
      order: 2;
    }

    &--slider {
      order: 3;
    }

    &--catalog {
      order: 4;
    }

    &--banner {
      order: 6;
    }

    &--popular {
      order: 5;
    }

    &--discount {
      order: 7;
    }

    &--tabs {
      order: 8;
    }

    &--hit {
      order: 9;
    }

    &--articles {
      order: 10;
    }

    &--about-us {
      order: 11;
    }

    &--contact {
      order: 12;
    }
  }
}

@media (min-width: 1024px) {
  .page-main {
    &--catalog-page {
      padding-top: 130px;
    }

    &--about-us {
      padding-top: 140px;
    }
    &__section {
      width: 100%;

      &--welcome {
        order: 1;
      }

      &--holyday {
        order: 2;
      }

      &--slider {
        order: 3;
      }

      &--catalog {
        order: 4;
      }

      &--banner {
        order: 5;
      }

      &--popular {
        order: 6;
      }

      &--discount {
        order: 7;
      }

      &--tabs {
        order: 8;
      }

      &--hit {
        order: 9;
      }

      &--articles {
        order: 10;
      }

      &--about-us {
        order: 11;
      }

      &--contact {
        order: 12;
      }
    }
  }
}
