.catalog-article {
    padding: 0 10px;

    &__title {
        font-family: Montserrat, sans-serif;
        font-style: normal;
        font-weight: bold;
        font-size: 14px;
        line-height: 22px;
        text-align: center;
        color: #2230A8;
    }

    &__text {
        font-family: Montserrat, sans-serif;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 25px;
        color: #000000;
        overflow: hidden;
        height: 209px;

        &--shown {
            height: auto;
        }
    }

    &__button {
        font-family: Montserrat, sans-serif;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 25px;
        color: #000000;
        overflow: hidden;
        opacity: 0.7;
        border: none;
        border-bottom: 1px solid #000;
        background-color: transparent;
    }
}

@media (min-width: 768px) {
    .catalog-article {
        &__title {
            font-weight: 600;
            font-size: 18px;
            line-height: 40px;
            color: #262626
        }

        &__text {
            font-family: Montserrat, sans-serif;
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            line-height: 25px;
            height: auto;
            width: 540px;
            margin: 0 auto;
            color: #000000;
        }

        &__button {
            display: none;
        }
    }

}

@media (min-width: 1024px) {
    .catalog-article {
        &__title {
            text-align: left;
            font-weight: 500;
            font-size: 18px;
        }

        &__text {

            width: auto;
            margin: 0;
        }
    }

    .catalog-section .catalog-article {
        grid-row: 2;
        grid-column: 2/5;

        &__title {
            text-align: left;
            font-weight: 500;
            font-size: 18px;
        }

        &__text {

            width: auto;
            margin: 0;
        }
    }
}