.video-section {
  &__wrapper {
    width: 100%;
    position: relative;

    & video {
      min-height: 440px;
      width: auto;
      max-width: 100%;
    }
  }

  &__button {
    position: absolute;
    width: 70px;
    height: 70px;
    border-radius: 50%;
    border: none;
    background-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 30 50' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M28.4047 15.9791L3.72213 0.823323C3.04831 0.409383 2.1616 0.391465 1.4717 0.777894C0.76924 1.17081 0.333008 1.91489 0.333008 2.71949V32.9114C0.333008 33.7146 0.767971 34.4581 1.46817 34.8516C1.80014 35.0379 2.17698 35.1366 2.55805 35.1366C2.96677 35.1366 3.36702 35.0242 3.71564 34.8117L28.3979 19.7755C29.0549 19.3754 29.4639 18.6488 29.4652 17.8794C29.4666 17.11 29.0603 16.3818 28.4047 15.9791Z' fill='white'/%3E%3C/svg%3E%0A"),
      linear-gradient(175deg, #ffbd71 0%, #ff8e0b 92.31%);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: 20px 10px, center;
    top: 50%;
    left: 50%;
    z-index: 2;
    transform: translate(-50%, -50%);

    &--pause {
      opacity: 0.4;
      background: linear-gradient(175deg, #ffbd71 0%, #ff8e0b 92.31%);

      &::before {
        position: absolute;
        content: "";
        width: 7px;
        height: 30px;
        background-color: #fff;
        top: 30%;
        left: 25px;
      }

      &::after {
        position: absolute;
        content: "";
        width: 7px;
        height: 30px;
        top: 30%;
        left: 38px;
        background-color: #fff;
      }
    }

    &--hidden {
      opacity: 0;
    }
  }
}

@media (min-width: 1024px) {
  .video-section {
    &__wrapper {
      width: 820px;
      margin-left: auto;
      margin-right: auto;
    }
  }
}

@media (min-width: 1200px) {
  .video-section {
    &__wrapper {
      width: 100vw;
      margin: 0;
      height: 644px;
      overflow: hidden;

      & video {
        position: relative;
        width: 100%;
        max-width: unset;
      }
    }
  }
}