.hit-section {
  background-color: #2230a8;

  &__img-wrapper {
    & img {
      filter: drop-shadow(-25.9384px 51.8768px 80px rgba(0, 0, 0, 0.13)) drop-shadow(-15.2725px 30.545px 41.728px rgba(0, 0, 0, 0.094016)) drop-shadow(-7.84377px 15.6875px 19.584px rgba(0, 0, 0, 0.073008));
    }
  }

  &__wrapper {
    padding-bottom: 72px;
  }

  &__title {
    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 70px;
    text-align: center;
    letter-spacing: 0.03em;
    color: #ffffff;
  }

  &__free {
    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 36px;
    line-height: 75px;
    /* or 208% */

    text-align: center;
    letter-spacing: 0.005em;
    text-transform: uppercase;

    color: #ffb054;
  }

  &__info {
    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 25px;
    text-align: center;
    width: 257px;
    margin: 0 auto;
    color: #ffffff;
  }

  &__sub-text {
    margin: 0;
    margin-top: -40px;
    margin-bottom: 22px;
    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 25px;
    /* or 208% */

    text-align: center;

    color: #ffffff;

    opacity: 0.8;
  }

  &__catalog-link {
    border-radius: 50px;
    background: #46bfff;
    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 45px;
    text-align: center;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: #ffffff;
    display: block;
  }
}

.page-main--about-us .hit-section {
  background-color: transparent;

  &__wrapper {
    background-image: url("../img/mokap-vizitok.png");
    background-repeat: no-repeat;
    background-position: 0 275px;
  }

  &__free {
    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: 800;
    font-size: 18px;
    text-align: center;
    color: #2230a8;
    text-transform: none;
  }

  &__text {
    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 25px;
    text-align: center;
    color: #2230a8;
  }

  &__sub-text {
    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 25px;
    text-align: center;
    color: #262626;
    margin-top: 30px;
    margin-bottom: 410px;
  }
}

