.pagination {
  @include list-reset;
  display: flex;
  margin: 0 auto;
  justify-content: center;

  &__item {
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;

    & a {
      text-align: center;
      font-family: Montserrat, sans-serif;
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: 25px;
      text-align: center;
      color: #2230a8;
    }

    &--current {
      border-radius: 50%;
      background-color: #2230a8;

      & a {
        font-family: Montserrat, sans-serif;
        font-style: normal;
        font-weight: 600;
        font-size: 14px;
        line-height: 25px;
        text-align: center;
        color: #ffffff;
      }
    }
  }
}


@media (min-width: 1200px) {
  .pagination {
    &__item {
      & a {
        font-size: 16px;
      }
    }
  }

  .page-main--catalog-page > .pagination {
    margin-top: -30px;
    margin-bottom: -30px;
    padding: 0;
  }
}
