.page-footer {
  width: 100%;
  margin: 0;
  background: #2230a8;

  &__wrapper {
    padding: 10px;
    padding-top: 20px;
  }

  &__info {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
  }

  &__list {
    @include list-reset;
    width: 50%;
    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 25px;
    color: #ffffff;
  }

  &__contacts {
    color: #fff;
    display: flex;
    flex-wrap: wrap;
  }

  &__link {
    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 25px;
    display: flex;
    align-items: center;

    color: #ffffff;

    &--phone {
      order: 1;
      margin-right: 40px;
    }

    &--email {
      order: 2;
    }
  }

  & .socials-list {
    order: 3;
  }

  &__address {
    order: 4;

    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 20px;
    /* or 167% */

    display: flex;
    align-items: center;
    width: 90%;
    color: #ffffff;
    margin-bottom: 20px;
  }

  &__time {
    order: 5;

    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 25px;
    display: flex;
    align-items: center;

    color: #ffffff;
    margin-bottom: 24px;
  }

  &__payment-list {
    order: 6;

    @include list-reset;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-bottom: 20px;
  }

  &__payment-item {
    width: 82px;
    overflow: hidden;

    &:not(:last-child) {
      margin-right: 14px;
    }
  }

  &__bottom {
    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 10px;
    line-height: 25px;
    text-align: left;
    color: #ffffff;
    display: flex;
    flex-direction: column;
    opacity: 0.5;
  }
}

