.page-header {
  position: sticky;
  position: -webkit-sticky;
  top: 0;
  z-index: 100500;
  width: 100%;
  background-color: #2230a8;


  &__top-wrapper,
  &__nav {
    max-width: 1146px;
    margin: 0 auto;
  }

  &__top-wrapper {
    display: flex;
    align-items: center;
    padding: 10px;
    padding-top: 12px;
    padding-bottom: 10px;
  }

  &__menu-button {
    background-color: transparent;
    border: none;
    padding: 0;

    & .menu-icon--closed {
      display: none;
    }

    & .menu-icon--opened {
      display: block;
    }

    &--closed {
      & .menu-icon--opened {
        display: none;
      }

      & .menu-icon--closed {
        display: block;
      }
    }
  }

  &__phone {
    display: none;
  }

  &__call-button {
    display: none;
  }

  &__search {
    width: 180px;
    position: relative;
    margin: 0 auto;

    & input {
      background: rgba(255, 255, 255, 0.3);
      backdrop-filter: blur(15px);
      border-radius: 50px;
      border: none;
      padding-left: 30px;
      max-width: 100%;
      height: 20px;
      color: #000;
      outline: none;


      &::placeholder {
        color: rgba(255, 255, 255, 0.7);
      }
    }

    & button {
      position: absolute;
      background-color: transparent;
      border: none;
      opacity: 0.7;
      top: 4px;
      left: 0;

      & svg {
        fill: transparent;
      }
    }
  }

  &--mobile-menu {
    height: 100vh;
    margin-bottom: -100vh;

    & .page-header__search {
      display: none;
    }

    & .button-icon {
      display: none;
    }

    & .page-header__menu-button {
      position: absolute;
      right: 10px;
      // page-header__menu-button--closed
    }
  }
}

@media (min-width: 768px) {
  .page-header {
    &--mobile-menu {
      background-color: transparent;
      background-image: linear-gradient(to right,
          #2230a8 0,
          #2230a8 374px,
          rgba(40, 40, 40, 0.5) 374px,
          rgba(40, 40, 40, 0.5) 100%);


      & .page-header__menu-button {
        right: 0;
        top: 0;
        height: 100vh;
        width: calc(100vw - 374px);
        outline: none;

        & svg {
          position: absolute;
          top: 15px;
          right: 20px;
        }
      }
    }

    &__top-wrapper {
      padding-left: 40px;
      padding-right: 25px;
    }

    &__menu-button {
      order: 1;
    }

    &__search {
      order: 3;
      margin-right: 25px;
      min-height: 26px;

      & input {
        min-height: 26px;
      }
    }

    & .button-icon {
      &--cart {
        order: 5;
      }

      &--fav {
        order: 4;
      }
    }
  }
}

@media (min-width: 1024px) {
  .page-header {
    background-color: transparent;
    margin-bottom: -116px;

    &--sticky {
      & .page-header__top-wrapper {
        display: none;
      }

      & .page-header__nav {
        margin: 0 auto;
        border-radius: 0 0 22px 22px;
      }
    }

    &__top-wrapper {
      padding: 0 18px;
      padding-top: 6px;
      padding-bottom: 8px;
      background-color: transparent;
      justify-content: space-between;
    }

    &__menu-button {
      display: none;
    }

    &__search {
      margin: 0;
      background: rgba(255, 255, 255, 0.3);
      border: 2px solid #46bfff;
      box-sizing: border-box;
      backdrop-filter: blur(25px);
      border-radius: 50px;
      min-width: 232px;

      &:focus-within {
        border: 3px solid #46bfff;
      }

      & button {
        background: #46bfff;
        border-radius: 50%;
        margin: 0;
        padding: 0;
        width: 30px;
        height: 30px;
        top: 0;
        left: -1px;

        & svg {
          fill: transparent;
        }
      }

      & input {
        width: 100%;
        padding-left: 42px;

        &::placeholder {
          font-family: Montserrat, sans-serif;
          font-style: normal;
          font-weight: normal;
          font-size: 14px;
          line-height: 25px;
          /* or 179% */

          display: flex;
          align-items: center;

          /* Gray 4 */

          color: #bdbdbd;
        }
      }
    }

    &__phone {
      display: block;
      background: rgba(255, 255, 255, 0.3);
      border: 2px solid #50d0bc;
      box-sizing: border-box;
      backdrop-filter: blur(25px);
      /* Note: backdrop-filter has minimal browser support */
      width: 232px;
      text-align: center;
      border-radius: 50px;
      order: 3;
      background-image: url("data:image/svg+xml,%3Csvg width='31' height='30' viewBox='0 0 31 30' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cg filter='url(%23filter0_b)'%3E%3Crect width='30.1299' height='30' rx='15' fill='%2350D0BC'/%3E%3C/g%3E%3Cpath d='M22.7046 18.0085L20.5978 15.9152C19.8454 15.1676 18.5662 15.4667 18.2653 16.4385C18.0395 17.1114 17.2871 17.4852 16.6099 17.3356C15.1051 16.9618 13.0736 15.0181 12.6974 13.4481C12.4716 12.7752 12.9231 12.0276 13.6003 11.8034C14.5784 11.5043 14.8794 10.2334 14.127 9.48578L12.0202 7.39249C11.4182 6.86917 10.5153 6.86917 9.98864 7.39249L8.55904 8.81294C7.12944 10.3081 8.70952 14.2705 12.2459 17.7842C15.7823 21.2979 19.7701 22.9427 21.275 21.4475L22.7046 20.027C23.2313 19.4289 23.2313 18.5318 22.7046 18.0085Z' fill='white'/%3E%3Cdefs%3E%3Cfilter id='filter0_b' x='-25' y='-25' width='80.1299' height='80' filterUnits='userSpaceOnUse' color-interpolation-filters='sRGB'%3E%3CfeFlood flood-opacity='0' result='BackgroundImageFix'/%3E%3CfeGaussianBlur in='BackgroundImage' stdDeviation='12.5'/%3E%3CfeComposite in2='SourceAlpha' operator='in' result='effect1_backgroundBlur'/%3E%3CfeBlend mode='normal' in='SourceGraphic' in2='effect1_backgroundBlur' result='shape'/%3E%3C/filter%3E%3C/defs%3E%3C/svg%3E%0A");
      background-repeat: no-repeat;
      background-position: 0 0;
    }

    &__call-button {
      order: 6;
      background: #ff4557;
      border-radius: 50px;
      font-family: Montserrat, sans-serif;
      font-style: normal;
      font-weight: 600;
      font-size: 14px;
      line-height: 29px;
      width: 232px;
      text-align: center;
      letter-spacing: 0.05em;
      text-transform: uppercase;
      border: none;
      color: #ffffff;

      display: flex;
      justify-content: center;
      align-items: center;

      &:hover {
        background: #46BFFF;
      }
    }
  }
}

@media (min-width: 1200px) {
  .page-header {
    background-color: transparent;
    margin-bottom: -142px;

    &__top-wrapper {
      padding-top: 20px;
      padding-bottom: 20px;
    }

    &__phone {
      min-height: 40px;
      display: flex;
      align-items: center;
      justify-content: center;
      background-position: -1px -1px;
      background-image: url("data:image/svg+xml,%3Csvg width='42' height='40' viewBox='0 0 42 40' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cg filter='url(%23filter0_b)'%3E%3Crect width='41.4118' height='40' rx='20' fill='%2350D0BC'/%3E%3C/g%3E%3Cpath d='M30.4736 24.9504L27.584 21.9174C26.5521 20.8341 24.7977 21.2675 24.3849 22.6756C24.0753 23.6506 23.0433 24.1922 22.1145 23.9755C20.0505 23.4339 17.2642 20.6175 16.7482 18.3427C16.4386 17.3678 17.0578 16.2846 17.9866 15.9597C19.3281 15.5264 19.7409 13.6849 18.7089 12.6017L15.8194 9.56869C14.9938 8.81044 13.7554 8.81044 13.033 9.56869L11.0722 11.6268C9.11145 13.7932 11.2786 19.5343 16.129 24.6254C20.9793 29.7165 26.4489 32.0996 28.5128 29.9332L30.4736 27.8751C31.196 27.0085 31.196 25.7086 30.4736 24.9504Z' fill='white'/%3E%3Cdefs%3E%3Cfilter id='filter0_b' x='-25' y='-25' width='91.4118' height='90' filterUnits='userSpaceOnUse' color-interpolation-filters='sRGB'%3E%3CfeFlood flood-opacity='0' result='BackgroundImageFix'/%3E%3CfeGaussianBlur in='BackgroundImage' stdDeviation='12.5'/%3E%3CfeComposite in2='SourceAlpha' operator='in' result='effect1_backgroundBlur'/%3E%3CfeBlend mode='normal' in='SourceGraphic' in2='effect1_backgroundBlur' result='shape'/%3E%3C/filter%3E%3C/defs%3E%3C/svg%3E%0A");
    }

    &__search {
      min-height: 40px;

      & input {
        height: 36px;
      }

      & button {
        height: 38px;
        width: 40px;

        & svg {
          width: 30px;
          height: 30px;
          fill: transparent;
        }
      }
    }

    &__call-button {

      &--main {
        background-color: #46BFFF;

        &:hover {
          background: #ff4557;
        }
      }
    }

    &--sticky {
      display: flex;
      flex-direction: row-reverse;

      & .page-header__top-wrapper {
        display: flex;
        border-radius: 0 0 22px 0;
        background-color: #2230a8;
        margin-left: 0;
        padding: 0;
        padding-top: 3px;

        & .page-header__call-button,
        & .page-header__phone,
        & .page-header__search,
        & .page-header__logo {
          display: none;
        }
      }

      & .page-header__nav {
        border-radius: 0 0 0 22px;
        margin-right: -1px;
      }
    }

    &__wrapper {
      max-width: 1146px;
      margin: 0 auto;
    }

    &__nav {
      max-width: 1146px;
      margin: 0 auto;
    }

    &__call-button {
      min-width: 237px;
      min-height: 40px;
    }
  }
}