.catalog-section {
  width: 100%;
  padding-top: 40px;

  .page-main--catalog-page & {
    &__title {
      display: none;
    }
  }

  &__title {
    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: 800;
    font-size: 18px;
    line-height: 75px;
    text-align: center;
    color: #2230a8;
    margin: 0;
  }

  &__menu-list {
    display: none;
    position: relative;
  }

  &__menu-list-wrapper {
    @include list-reset;
    position: sticky;
    top: 50px;
    border-radius: 30px;
    overflow: hidden;
  }

  &__goods-list {
    @include list-reset;
  }

  &__sublist {
    @include list-reset;
  }
}

@media (min-width: 768px) {
  .catalog-section {
    &__goods-list {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      width: 95%;
      margin: 0 auto;
    }

    &__goods-item {
      width: 354px;
    }

    .page-main--catalog-page & {
      &__goods-list {
        margin: 0 auto;
      }
    }
  }
}

@media (min-width: 1024px) {
  .page-main--catalog-page .catalog-section {
    .catalog-section {
      padding-top: 0;

      &__wrapper {
        display: grid;
        grid-template-columns: 232px 232px 232px 232px;
        grid-template-rows: 60px 267px 1fr;
        gap: 2%;
      }

      &__menu-list {
        grid-column-start: 1;
        grid-column-end: 2;
        grid-row-start: 1;
        grid-row-end: 6;
        min-width: 232px;
      }

      &__button {
        padding-left: 20px;
      }

      &__sublist {
        padding-left: 35px;
      }

      &__goods-list {
        grid-column: 2/5;
        grid-row: 3/4;
        display: flex;
        justify-content: flex-start;
        min-width: 736px;
        width: 100%;

        padding: 0;
        margin: 0;
      }
    }

    & .pagination {
      grid-column: 2/5;
      grid-row: 1/2;
    }

    & .catalog-banner {
      grid-column: 2/5;
      grid-row: 2/3;
      margin: 0;
      width: 100%;
    }
  }

  .catalog-section {
    padding-bottom: 120px;

    &__wrapper {
      padding: 18px;
      display: flex;
      justify-content: space-between;
    }

    &__title {
      display: none;
    }

    &__menu-list {
      display: block;
      min-width: 316px;
      background: rgba(255, 255, 255, 0.3);
      backdrop-filter: blur(25px);
      border-radius: 30px;
      @include list-reset;
      margin-right: auto;
    }

    &__sublist {
      display: none;
      padding-left: 75px;
    }

    &__item {
      font-family: Montserrat, sans-serif;
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      line-height: 25px;
      /* identical to box height, or 156% */

      display: flex;
      align-items: center;
      margin-bottom: 10px;
      color: #2230a8;
      position: relative;

      &:hover,
      &:focus-within {
        text-indent: 20px;

        &::before {
          position: absolute;
          content: "";
          background-image: url("data:image/svg+xml,%3Csvg width='32' height='16' viewBox='0 0 32 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M30.961 8.70711C31.3516 8.31658 31.3516 7.68342 30.961 7.29289L24.5971 0.928932C24.2066 0.538408 23.5734 0.538408 23.1829 0.928932C22.7923 1.31946 22.7923 1.95262 23.1829 2.34315L28.8397 8L23.1829 13.6569C22.7923 14.0474 22.7923 14.6805 23.1829 15.0711C23.5734 15.4616 24.2066 15.4616 24.5971 15.0711L30.961 8.70711ZM0.158691 9H30.2539V7H0.158691V9Z' fill='%232230A8'/%3E%3C/svg%3E%0A");
          width: 32px;
          height: 16px;
          left: -15px;
        }
      }
    }

    &__button {
      border: none;
      background-color: transparent;
      font-family: Montserrat, sans-serif;
      font-style: normal;
      font-weight: bold;
      font-size: 18px;
      line-height: 25px;
      display: flex;
      align-items: center;
      padding-left: 50px;
      height: 60px;
      width: 100%;
      color: #2230a8;
      background-image: url("data:image/svg+xml,%3Csvg width='316' height='60' viewBox='0 0 316 60' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cg filter='url(%23filter0_b)'%3E%3Crect width='316' height='60' fill='url(%23paint0_linear)' fill-opacity='0.3'/%3E%3Crect x='2' y='2' width='312' height='56' stroke='%23E0DFDF' stroke-opacity='0.1' stroke-width='4'/%3E%3Crect x='2' y='2' width='312' height='56' stroke='url(%23paint1_linear)' stroke-width='4'/%3E%3C/g%3E%3Cdefs%3E%3Cfilter id='filter0_b' x='-15' y='-15' width='346' height='90' filterUnits='userSpaceOnUse' color-interpolation-filters='sRGB'%3E%3CfeFlood flood-opacity='0' result='BackgroundImageFix'/%3E%3CfeGaussianBlur in='BackgroundImage' stdDeviation='7.5'/%3E%3CfeComposite in2='SourceAlpha' operator='in' result='effect1_backgroundBlur'/%3E%3CfeBlend mode='normal' in='SourceGraphic' in2='effect1_backgroundBlur' result='shape'/%3E%3C/filter%3E%3ClinearGradient id='paint0_linear' x1='158' y1='0' x2='158' y2='60' gradientUnits='userSpaceOnUse'%3E%3Cstop stop-color='white'/%3E%3Cstop offset='1' stop-color='white' stop-opacity='0'/%3E%3C/linearGradient%3E%3ClinearGradient id='paint1_linear' x1='291.748' y1='55.8023' x2='203.752' y2='-104.764' gradientUnits='userSpaceOnUse'%3E%3Cstop stop-color='white' stop-opacity='0.8'/%3E%3Cstop offset='1' stop-color='white' stop-opacity='0'/%3E%3C/linearGradient%3E%3C/defs%3E%3C/svg%3E%0A");
      background-size: auto;
      background-position: right;
      background-repeat: no-repeat;
      margin-bottom: -2px;

      &--closed span {
        position: relative;

        &::before {
          position: absolute;
          display: block;
          content: "";
          background-image: url("data:image/svg+xml,%3Csvg width='9' height='9' viewBox='0 0 9 9' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M7.71015 3.40221C8.40476 3.78128 8.40476 4.77872 7.71015 5.15779L2.41886 8.04545C1.75248 8.40911 0.939816 7.92681 0.939816 7.16765V1.39235C0.939816 0.63319 1.75248 0.150886 2.41886 0.514555L7.71015 3.40221Z' fill='%232230A8'/%3E%3C/svg%3E%0A");
          width: 9px;
          height: 9px;
          right: -15px;
          top: 30%;
        }
      }

      &--opened {
        &~.catalog-section__sublist {
          display: block;
        }

        & span {
          position: relative;

          &::before {
            position: absolute;
            display: block;
            content: "";
            background-image: url("data:image/svg+xml,%3Csvg width='9' height='9' viewBox='0 0 9 9' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M7.71015 3.40221C8.40476 3.78128 8.40476 4.77872 7.71015 5.15779L2.41886 8.04545C1.75248 8.40911 0.939816 7.92681 0.939816 7.16765V1.39235C0.939816 0.63319 1.75248 0.150886 2.41886 0.514555L7.71015 3.40221Z' fill='%232230A8'/%3E%3C/svg%3E%0A");
            width: 9px;
            height: 9px;
            right: -15px;
            top: 30%;
            transform: rotate(90deg);
          }
        }
      }
    }

    &__menu-item {
      &:first-child {
        & .catalog-section__button {
          background-image: url("data:image/svg+xml,%3Csvg width='316' height='60' viewBox='0 0 316 60' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cg filter='url(%23filter0_b)'%3E%3Cpath d='M0 30C0 13.4315 13.4315 0 30 0H286C302.569 0 316 13.4315 316 30V60H0V30Z' fill='url(%23paint0_linear)' fill-opacity='0.3'/%3E%3Cpath d='M2 30C2 14.536 14.536 2 30 2H286C301.464 2 314 14.536 314 30V58H2V30Z' stroke='%23E0DFDF' stroke-opacity='0.1' stroke-width='4'/%3E%3Cpath d='M2 30C2 14.536 14.536 2 30 2H286C301.464 2 314 14.536 314 30V58H2V30Z' stroke='url(%23paint1_linear)' stroke-width='4'/%3E%3C/g%3E%3Cdefs%3E%3Cfilter id='filter0_b' x='-15' y='-15' width='346' height='90' filterUnits='userSpaceOnUse' color-interpolation-filters='sRGB'%3E%3CfeFlood flood-opacity='0' result='BackgroundImageFix'/%3E%3CfeGaussianBlur in='BackgroundImage' stdDeviation='7.5'/%3E%3CfeComposite in2='SourceAlpha' operator='in' result='effect1_backgroundBlur'/%3E%3CfeBlend mode='normal' in='SourceGraphic' in2='effect1_backgroundBlur' result='shape'/%3E%3C/filter%3E%3ClinearGradient id='paint0_linear' x1='158' y1='0' x2='158' y2='60' gradientUnits='userSpaceOnUse'%3E%3Cstop stop-color='white'/%3E%3Cstop offset='1' stop-color='white' stop-opacity='0'/%3E%3C/linearGradient%3E%3ClinearGradient id='paint1_linear' x1='291.748' y1='55.8023' x2='203.752' y2='-104.764' gradientUnits='userSpaceOnUse'%3E%3Cstop stop-color='white' stop-opacity='0.8'/%3E%3Cstop offset='1' stop-color='white' stop-opacity='0'/%3E%3C/linearGradient%3E%3C/defs%3E%3C/svg%3E%0A");
        }
      }

      &:last-child {
        & .catalog-section__button {
          background-image: url("data:image/svg+xml,%3Csvg width='316' height='60' viewBox='0 0 316 60' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cg filter='url(%23filter0_b)'%3E%3Cpath d='M0 0H316V30C316 46.5685 302.569 60 286 60H30C13.4315 60 0 46.5685 0 30V0Z' fill='url(%23paint0_linear)' fill-opacity='0.3'/%3E%3Cpath d='M2 2H314V30C314 45.464 301.464 58 286 58H30C14.536 58 2 45.464 2 30V2Z' stroke='%23E0DFDF' stroke-opacity='0.1' stroke-width='4'/%3E%3Cpath d='M2 2H314V30C314 45.464 301.464 58 286 58H30C14.536 58 2 45.464 2 30V2Z' stroke='url(%23paint1_linear)' stroke-width='4'/%3E%3C/g%3E%3Cdefs%3E%3Cfilter id='filter0_b' x='-15' y='-15' width='346' height='90' filterUnits='userSpaceOnUse' color-interpolation-filters='sRGB'%3E%3CfeFlood flood-opacity='0' result='BackgroundImageFix'/%3E%3CfeGaussianBlur in='BackgroundImage' stdDeviation='7.5'/%3E%3CfeComposite in2='SourceAlpha' operator='in' result='effect1_backgroundBlur'/%3E%3CfeBlend mode='normal' in='SourceGraphic' in2='effect1_backgroundBlur' result='shape'/%3E%3C/filter%3E%3ClinearGradient id='paint0_linear' x1='158' y1='0' x2='158' y2='60' gradientUnits='userSpaceOnUse'%3E%3Cstop stop-color='white'/%3E%3Cstop offset='1' stop-color='white' stop-opacity='0'/%3E%3C/linearGradient%3E%3ClinearGradient id='paint1_linear' x1='291.748' y1='55.8023' x2='203.752' y2='-104.764' gradientUnits='userSpaceOnUse'%3E%3Cstop stop-color='white' stop-opacity='0.8'/%3E%3Cstop offset='1' stop-color='white' stop-opacity='0'/%3E%3C/linearGradient%3E%3C/defs%3E%3C/svg%3E%0A");
        }
      }
    }

    &__goods-list {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      width: 95%;
      margin: 0 auto;
      max-width: 650px;
    }

    &__goods-item {
      width: 316px;
      min-width: 316px;
    }
  }
}

@media (min-width: 1200px) {
  .catalog-section {
    &__wrapper {
      max-width: 1146px;
      margin: 0 auto;
    }
  }

  .page-main--catalog-page .catalog-section .catalog-section {

    &__wrapper {
      gap: 30px;

      grid-template-columns: 264px 264px 264px 264px;
    }

    &__menu-list {
      min-width: 264px;
    }
  }
}