.socials-list__item {
    flex-shrink: 0;
    
    display: flex;
      
    justify-content: center;
    align-items: center;
  }
  .socials-list__item:not(:last-child) {
    margin-right: 10px; 
  }
    
  .socials-list__item a {
    width: 35px;
    height: 35px;
    
    background-position: center;
    background-repeat: no-repeat;
    background-size: 100%;
    
    transition: background-image 0.3s;
  }
  
  .socials-list__item--instagram a {
    background-image: url("../img/icon-instagram.svg");
  }
  .socials-list__item--telegram a {
    background-image: url("../img/icon-telegram.svg");
  }
  .socials-list__item--youtube a {
    background-image: url("../img/icon-youtube.svg");
  }
  .socials-list__item--vk a {
    background-image: url("../img/icon-vk.svg");
  }
  
  .socials-list__item--tik-tok a {
    background-image: url("../img/icon-tik-tok.svg");
  }
  
  .socials-list__item--whatsapp a {
    background-image: url("../img/icon-whatsapp.svg");
  }
  
  /* hover/focus */
  
  .socials-list__item a:hover,
  .socials-list__item a:focus {
    transition: background-image 0.3s;
  }
  
  .socials-list__item--instagram a:hover,
  .socials-list__item--instagram a:focus {
    background-image: url("../img/icon-instagram-2.svg");
  }
  .socials-list__item--telegram a:hover,
  .socials-list__item--telegram a:focus {
    background-image: url("../img/icon-telegram-2.svg");
  }
  .socials-list__item--youtube a:hover,
  .socials-list__item--youtube a:focus {
    background-image: url("../img/icon-youtube-2.svg");
  }
  .socials-list__item--vk a:hover,
  .socials-list__item--vk a:focus {
    background-image: url("../img/icon-vk-2.svg");
  }
  
  .socials-list__item--tik-tok a:hover,
  .socials-list__item--tik-tok a:focus {
    background-image: url("../img/icon-tik-tok-2.svg");
  }
  
  .socials-list__item--whatsapp a:hover,
  .socials-list__item--whatsapp a:focus {
