.link-list {
  @include list-reset;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  padding: 10px;
  color: #262626;
  margin-left: -6%;

  &__item {
    text-align: center;
    width: 140px;
    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 25px;
    text-align: center;
    margin-bottom: 30px;
    margin-left: 6%;
    color: inherit;
    position: relative;

    & img {
      border-radius: 20px;
      overflow: hidden;
    }
  }
}

@media (min-width: 768px) {
  .link-list {
    margin: 0 auto;
    margin-left: 1%;
    width: 95%;

    &__item {
      width: 167px;
      min-height: 208px;
      margin-left: 1%;

      & img {
        border-radius: 20px;
        overflow: hidden;
      }
    }
  }
}

@media (min-width: 1024px) {
  .link-list {
    max-width: 100%;
    width: calc(99%);

    &__item {
      width: 232px;
      min-height: 290px;

      & img {
        border-radius: 30px;
        overflow: hidden;
      }
    }
  }

  .page-main--catalog-page .catalog-section {
    .link-list {
      &__item {

        & img {
          border-radius: 30px;
          overflow: hidden;
        }

        &:nth-child(n * 3) {
          margin-right: 0;
        }

        & a {
          font-family: Montserrat, sans-serif;
          font-style: normal;
          font-weight: 600;
          font-size: 16px;
          line-height: 25px;
          text-align: center;
          /*  */

          color: #262626;
        }
      }
    }
  }
}

@media (min-width: 1200px) {
  .link-list {
    &__item {
      width: 264px;

      &--bold {

        font-weight: 600;
        font-size: 16px;
        line-height: 25px;
      }

      & img {
        border-radius: 50px;
        overflow: hidden;
      }
    }
  }

  .page-main--catalog-page .catalog-section .link-list {
    &__wrapper {
      max-width: 1146px;
      margin: 0 auto;
    }

    &__item {
      margin-right: 1%;

      &:nth-child(n * 3) {
        margin-right: 0;
      }
    }
  }
}