.page-info {
    width: 100%;
    background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0, #fff 40.07%);
    padding-bottom: 72px;

    &--delivery {
        & .page-info__wrapper {
            background-image: url("../img/dostavka-avto-mobile.png"), url("../img/dostavka-background-mobile.png");

            background-size: contain, contain;
        }
    }

    &--payment {
        & .page-info__wrapper {
            background-image: url("../img/oplata-card-mobile.png"), url("../img/oplata-background-mobile.png");
            background-size: 50%, contain;
        }
    }

    &--refund {
        & .page-info__wrapper {
            background-image: url("../img/vozvrat-arrow-mobile.png"), url("../img/vozvrat-background-mobile.png");
            background-size: 264px 271px, contain;
        }
    }

    &--agreement {
        & .page-info__wrapper {
            background-image: url("../img/agreement-folders-mobile.png"), url("../img/agreement-background-mobile.png");
            background-size: 239px 182px, contain;
        }
    }

    &__wrapper {
        padding: 0 10px;
        padding-top: 85%;
        background-repeat: no-repeat;
        background-position: 50% 10%, top center;
    }

    &__text {
        margin: 0;
        font-family: Montserrat, sans-serif;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 25px;
        color: #000000;
    }
}

@media (min-width: 768px) {
    .page-info {
        &--delivery {
            & .page-info__wrapper {
                background-image: url("../img/dostavka-avto-tab.png"), url("../img/dostavka-background-tab.png");

                background-size: 603px, contain;
            }
        }

        &--payment {
            & .page-info__wrapper {
                background-image: url("../img/oplata-card-tab.png"), url("../img/oplata-background-tab.png");
                background-size: 277px 330px, contain;
            }
        }

        &--refund {
            & .page-info__wrapper {
                background-image: url("../img/refund-arrow-tab.png"), url("../img/refund-background-tab.png");
                background-size: 385px 395px, contain;
            }
        }

        &--agreement {
            & .page-info__wrapper {
                padding-top: 300px;

                background-image: url("../img/agreement-folders-tab.png"), url("../img/agreement-background-tab.png");
                background-position: 50% 30%, top;
                background-size: 370px 280px, cover;
            }
        }

        &__wrapper {
            padding: 0 10px;
            padding-top: 400px;
            background-repeat: no-repeat;
            background-position: 50% 10%, top center;
        }

        &__text {
            margin: 0 auto;
            max-width: 541px;
            font-size: 14px;
        }

        &__br--mobile-desktop {
            display: none;
        }
    }
}

@media (min-width: 1024px) {
    .page-info {
        &--delivery {
            & .page-info__wrapper {
                padding-top: 500px;
                background-image: url("../img/dostavka-avto-laptop.png"), url("../img/dostavka-background-laptop.png");

                background-size: 839px, 100%;
            }
        }

        &--payment {
            & .page-info__wrapper {
                background-image: url("../img/oplata-card-laptop.png"), url("../img/oplata-background-laptop.png");
                background-size: 290px 346px, 100%;
            }
        }

        &--refund {
            & .page-info__wrapper {
                background-image: url("../img/refund-arrow-laptop.png"), url("../img/refund-background-laptop.png");
                background-size: 444px 456px, 100%;
            }
        }

        &--agreement {
            & .page-info__wrapper {
                padding-top: 500px;

                background-image: url("../img/agreement-folders-laptop.png"), url("../img/agreement-background-laptop.png");
                background-position: 50% 30%, top;
                background-size: 460px 349px, cover;
            }
        }

        &__text {
            margin: 0 auto;
            max-width: 817px;
            font-size: 16px;
        }
    }
}


@media (min-width: 1200px) {
    .page-info {
        &__br--mobile-desktop {
            display: block;
        }

        &__wrapper {
            max-width: 1145px;
            margin: 0 auto;
        }

        &--delivery {
            & .page-info__wrapper {
                padding-top: 700px;
                background-image: url("../img/delivery-avto-desktop.png"), url("../img/delivery-background-desktop.png");

                background-size: 1010px, 100%;
            }
        }

        &--payment {
            & .page-info__wrapper {
                background-image: url("../img/payment-card-desktop.png"), url("../img/payment-background-desktop.png");
                background-size: 290px 346px, 100%;
            }
        }

        &--refund {
            & .page-info__wrapper {
                background-image: url("../img/refund-arrow-desktop.png"), url("../img/refund-background-desktop.png");
                background-size: 444px 456px, 100%;
            }
        }

        &--agreement {
            & .page-info__wrapper {
                padding-top: 500px;

                background-image: url("../img/agreement-folders-desktop.png"), url("../img/agreement-background-desktop.png");
                background-position: 50% 30%, top;
                background-size: 460px 349px, 100%;
            }
        }

        &__text {
            max-width: 1145px;
        }
    }
}