@media (min-width: 1024px) {
  .catalog-section .list-section {
    grid-row: 5;
    grid-column: 2;
  }
}

@media (min-width: 1200px) {
  .page-main--catalog-page {
    & .list-section {
      &__wrapper {
        max-width: 1146px;
        margin: 0 auto;
      }
    }
  }

  .catalog-section .list-section {
    grid-row: 5;
    grid-column: 2/5;
  }
}