.holyday-section {
  margin-top: -20px;
  padding-top: 50px;
  padding-bottom: 50px;
  padding-left: 25px;
  background-image: linear-gradient(to bottom,
      transparent 0,
      transparent 16px,
      #46bfff 16px,
      #46bfff 100%);
  background-repeat: no-repeat;
  background-position: 100% 5px;

  &__wrapper {
    display: block;
    width: 100%;
    margin: 0;
    padding: 0;
    position: relative;
  }

  &__img-wrapper {
    position: absolute;
    right: 0;
    top: 75px;
    width: 178px;
    z-index: 20;
  }

  &__title {
    margin: 0;
    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: 800;
    font-size: 18px;
    line-height: 20px;
    text-transform: uppercase;
    margin-bottom: 15px;
    color: #ffffff;

    & span {
      display: block;
    }
  }

  &__br-mobile {
    display: inline;
  }

  &__text {
    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 20px;

    color: #ffffff;
    margin: 0;
  }
}

@media (min-width: 768px) {
  .holyday-section {
    background-image: none;
    padding-left: 0;
    margin-top: -50px;
    text-align: center;

    &__wrapper {
      background-image: url("../img/podlozhka-aktsii.png");
      min-height: 201px;
      background-repeat: no-repeat;
      background-position: center;
      position: relative;
      padding-left: 220px;
      padding-top: 66px;
    }

    &__img-wrapper {
      position: absolute;
      right: unset;
      top: 20px;
      width: 340px;
      left: 0;
    }

    &__title {
      text-align: center;
      font-family: Montserrat, sans-serif;
      font-style: normal;
      font-weight: 800;
      font-size: 24px;
      line-height: 35px;
      color: #262626;
      text-transform: none;
      margin-bottom: 0;
      white-space: wrap;

      & span {
        display: inline;
        text-transform: uppercase;
        padding-right: 10px;
      }
    }

    &__text {
      font-family: Montserrat, sans-serif;
      font-style: normal;
      font-weight: normal;
      text-align: center;
      font-size: 18px;
      line-height: 40px;

      color: #262626;
    }

    &__br-mobile {
      display: none;
    }
  }
}

@media (min-width: 1024px) {
  .holyday-section {
    &__title {
      font-weight: 800;
      font-size: 36px;
    }

    &__img-wrapper {
      position: absolute;
      right: unset;
      top: 0;
      width: 400px;
      left: 0;
    }

    &__text {
      font-weight: normal;
      font-size: 24px;
    }
  }
}

@media (min-width: 1200px) {
  .holyday-section {
    &__wrapper {
      padding-left: 0;
    }

    &__title {
      margin-left: 190px;
      width: 49%;
      margin-left: auto;
      text-align: left;
    }

    &__img-wrapper {
      position: absolute;
      right: unset;
      top: -40px;
      width: auto;
      left: 50%;
      transform: translate(-102%);
    }

    &__text {
      margin-left: 190px;
      width: 50%;
      margin-left: auto;
      text-align: left;
    }
  }
}