.button-icon {
  background-color: transparent;
  border: none;
  margin: 0;
  padding: 0;
  position: relative;
  min-width: 20px;
  height: 20px;

  & svg {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  &--cart {
    top: -2px;

    & .button-icon__icon--mobile {
      fill-opacity: 0;
    }

    & .button-icon__icon--desktop {
      display: none;
    }
  }

  &--fav {
    margin-right: 7px;

    & .button-icon__icon--mobile {
      display: block;
      fill-opacity: 0;
      stroke: #FF4557;
    }

    & .button-icon__icon--desktop {
      display: none;
    }
  }
}

@media (min-width: 1024px) {
  .button-icon {
    &--fav {
      margin-right: 7px;
      background: rgba(255, 255, 255, 0.55);
      border: 2px solid #ff4557;
      box-sizing: border-box;
      backdrop-filter: blur(25px);
      width: 30px;
      height: 30px;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;

      & .button-icon__icon--mobile {
        display: none;
      }

      & .button-icon__icon--desktop {
        display: block;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 18px;
        height: 18px;
      }
    }

    &--cart {
      margin-right: 7px;
      background: rgba(255, 255, 255, 0.55);
      border: 2px solid #2230a8;
      box-sizing: border-box;
      backdrop-filter: blur(25px);
      /* Note: backdrop-filter has minimal browser support */
      width: 30px;
      height: 30px;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;

      & .button-icon__icon--mobile {
        display: none;
      }

      & .button-icon__icon--desktop {
        display: block;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
    }
  }
}

@media (min-width: 1200px) {
  .button-icon {
    &--fav {
      width: 40px;
      height: 40px;

      & .button-icon__icon--mobile {
        display: none;
      }

      & .button-icon__icon--desktop {
        display: block;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 24px;
        height: 24px;
      }
    }

    &--cart {
      margin-right: 7px;
      background: rgba(255, 255, 255, 0.55);
      border: 2px solid #2230a8;
      box-sizing: border-box;
      backdrop-filter: blur(25px);
      /* Note: backdrop-filter has minimal browser support */
      width: 40px;
      height: 40px;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;

      & .button-icon__icon--mobile {
        display: none;
      }

      & .button-icon__icon--desktop {
        display: block;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 24px;
        height: 24px;
      }
    }
  }

  .page-header--sticky {
    & .button-icon {
      &--fav {
        background-color: transparent;
        border: none;
        margin: 0;

        & .button-icon__icon--mobile {
          display: block;
        }

        & .button-icon__icon--desktop {
          display: none;
        }

        & svg {
          fill: transparent;
          stroke-width: 2px;
          stroke: white;
        }
      }

      &--cart {
        margin-right: 0;
        width: 40px;
        height: 40px;
        background-color: #fff;
        padding: 0;
        margin: 0;
        margin-top: 1px;
      }
    }
  }
}