    background-repeat: no-repeat;
    background-size: 146px;
    border-radius: 50px;
    border: none;
    padding-left: 66px;
    padding-right: 36px;
    color: #ffffff;
    display: block;
    margin: 0 auto;
  }
}

@media (min-width: 768px) {
  .catalog-banner {
    width: 95%;
    margin: 0 auto;
    background-image: url("../img/podlozhka.png");
    background-size: contain;

