.callback-form {
    &__text {
        font-family: Montserrat, sans-serif;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 20px;
        margin: 0 auto;
        margin-bottom: 20px;
        max-width: 90%;
        padding-top: 10px;

        display: flex;
        align-items: center;
        text-align: center;

        color: #262626
    }

    &__wrapper {
        width: 241px;
        margin: 0 auto;

    }

    &__input {
        display: none;
    }

    &__button {
        border-radius: 50px;
        background: #FF4557;
        font-family: Montserrat, sans-serif;
        font-style: normal;
        font-weight: 600;
        font-size: 14px;
        line-height: 45px;
        width: 100%;
        border: none;
        text-align: center;
        letter-spacing: 0.05em;
        text-transform: uppercase;

        color: #FFFFFF;
        display: none;

        &--link {
            display: block;
        }
    }
}

@media (min-width: 768px) {
    .callback-form {

        &__text {
            font-weight: normal;
            font-size: 16px;
            line-height: 25px;
            text-align: left;
            margin: 0;
            margin-bottom: 30px;
        }

        &__input {
            display: inline;
            font-family: Montserrat, sans-serif;
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            line-height: 25px;
            text-align: center;
            color: #828282;
            background: rgba(255, 255, 255, 0.3);
            border: 2px solid #FF4557;
            box-sizing: border-box;
            backdrop-filter: blur(25px);
            z-index: 10;
            border-radius: 50px;
            width: 190px;
        }

        &__wrapper {
            display: flex;
            width: 450px;
            position: relative;
            margin: 0;
        }

        &__button {
            position: relative;
            left: -20px;
            display: block;

            &--link {
                display: none;
            }
        }
    }
}

@media (min-width: 1024px) {
    .callback-form {
        &__text {
            width: 406px;
        }
    }
}