.good-article {

  &__title,
  &__new,
  &__discount,
  &__cart,
  &__close,
  &__favorite,
  &__img-list {
    display: none;
  }
}

@media (min-width: 768px) {
  .good-article {
    &--popup {
      position: fixed;
      width: 100vw;
      z-index: 10;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      background: rgba(255, 255, 255, 0.85);
      backdrop-filter: blur(15px);
      padding: 50px 6.5vw;
      left: 0;
      top: 20%;

      & .good-article {

        &__title,
        &__new,
        &__discount,
        &__cart,
        &__close,
        &__favorite,
        &__img-list {
          display: block;
        }

        &__img-list {
          @include list-reset;
          width: 80px;
        }

        &__img-item {
          border-radius: 17px;
          overflow: hidden;
        }

        &__img-wrapper {
          border-radius: 50px;
          overflow: hidden;

          & img {
            border-radius: 50px;
            overflow: hidden;
          }

          width: 315px;
        }

        &__info-wrapper {
          display: flex;
          flex-direction: column;
        }

        &__title {
          font-family: Montserrat, sans-serif;
          font-style: normal;
          font-weight: 500;
          font-size: 16px;
          line-height: 25px;
          margin: 0;
          margin-top: 63px;
          margin-bottom: 10px;
          text-align: left;
        }

        &__price {
          font-weight: 500;
          font-size: 18px;
          line-height: 25px;
          text-align: left;
          display: block;
          margin-bottom: 75px;
        }

        &__favorite {
          color: rgba(38, 38, 38, 0.5);
          display: flex;
          align-items: center;
          background-color: transparent;
          border: none;
          font-weight: normal;
          font-size: 14px;

          & svg {
            fill: rgba(255, 255, 255, 0);
            stroke: #FF4557;
            margin-right: 20px;
          }
        }

        &__discount,
        &__new {
          position: absolute;
          width: 56.86px;
          height: 35.38px;
          background: #2230a8;
          box-sizing: border-box;
          backdrop-filter: blur(25px);
          border-radius: 50px;
          color: #fff;
          display: flex;
          align-items: center;
          justify-content: center;
          top: 56px;
          left: calc(50% + 50px);
          text-transform: lowercase;
        }

        &__discount {
          background: #ff4557;
        }

        &__close {
          position: absolute;
          width: 20px;
          height: 20px;
          background-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 18 17' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 15.7631L16.28 1' stroke='%232230A8' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M16.28 15.7631L1 1L16.28 15.7631Z' stroke='%232230A8' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
          background-repeat: no-repeat;
          background-size: contain;
          border: none;
          background-color: transparent;
          top: 23px;
          right: 30px;
        }

        &__cart {
          width: 215px;
          height: 50px;
          background: #28348a;
          box-sizing: border-box;
          backdrop-filter: blur(25px);
          font-family: Montserrat, sans-serif;
          border: none;
          font-style: normal;
          font-weight: 600;
          font-size: 16px;
          line-height: 25px;
          text-align: center;
          color: #ffffff;
          border-radius: 50px;
          text-transform: uppercase;
          margin-top: auto;
          margin-bottom: 5px;
          order: 1;

          & svg {
            display: none;
          }

          &:hover svg {
            fill: #FF4557;
          }
        }
      }
    }
  }
}

@media (min-width: 1024px) {
  .good-article {

    &__cart,
    &__favorite {
      display: block;
      background-color: transparent;
      border: none;

      & svg {
        display: inline;
        fill: rgba(255, 255, 255, 0);
      }



      & span {
        overflow: hidden;
        position: absolute;
        width: 0;
        height: 0;
      }
    }

    &__favorite {
      & svg {
        stroke: #FF4557;
      }

      &:hover svg {
        fill: #ff4557;
      }
    }

    &__cart {
      margin-left: auto;
      margin-top: -2px;

      &:hover {
        & svg {
          display: none;
        }

        width: 32px;
        background-image: url('../img/Basket(1).png');
        background-size: 20px 20px;
        background-position: center;
        background-repeat: no-repeat;
      }
    }

    &__info-wrapper {
      display: flex;
    }

    &__price {
      font-weight: 500;
      font-size: 14px;
      text-align: left;
    }

    &--popup {
      width: 820px;
      left: 50%;
      transform: translate(-50%);
      border-radius: 50px;

      & .good-article {

        &__cart,
        &__favorite {
          & span {
            overflow: visible;
            position: static;
            width: auto;
            height: auto;
          }
        }

        &__cart {
          margin-top: 30px;

          &:hover {

            width: 215px;
            background-image: none;
          }
        }
      }
    }
  }
}

@media (min-width: 1200px) {
  .good-article {

    &__discount,
    &__new {
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      z-index: 100;
      background: #2230a8;
      /*  */
      padding: 3px;
      width: 45px;
      min-height: 28px;
      box-sizing: border-box;
      backdrop-filter: blur(25px);
      border-radius: 50px;
      color: #fff;
      text-transform: lowercase;
    }

    &__discount {
      background: #ff4557;
    }
  }

  .link-list__item {
    overflow: visible;

    &:hover .good-article {
      position: absolute;
      background-color: #fff;
      width: 445px;
      display: flex;
      flex-direction: column;
      flex-wrap: wrap;
      left: -130px;
      top: -35px;
      padding: 30px;
      padding-top: 35px;
      background: rgba(255, 255, 255, 0.75);
      backdrop-filter: blur(15px);
      border-radius: 50px;
      z-index: 100000;
      height: 520px;
      transition: background-color 200ms;

      &__discount,
      &__new {
        top: 35px;
        right: 50px;
      }

      &__cart,
      &__favorite {
        display: block;
        background-color: transparent;
        border: none;

        & svg {
          display: inline;
          fill: rgba(255, 255, 255, 0);

        }

        & span {
          overflow: hidden;
          position: absolute;
          width: 0;
          height: 0;
        }
      }

      &__favorite {
        &:hover svg {
          fill: #ff4557;
        }
      }

      &__img-list {
        display: block;
        width: 80px;
        @include list-reset;
        margin-right: 20px;
      }

      &__img-wrapper {
        width: 264px;
        min-height: 330px;
      }

      &__info-wrapper {
        display: flex;
        flex-wrap: wrap;
      }

      &__price {
        align-self: start;
        display: block;
        width: 100%;
        order: 1;
        margin-bottom: 25px;
      }

      &__favorite {
        order: 3;
        margin-left: 16px;
      }

      &__cart {
        margin: 0;
        background: #28348a;
        box-sizing: border-box;
        backdrop-filter: blur(25px);
        border-radius: 50px;
        width: 200px;
        height: 50px;
        order: 2;

        & svg {
          display: none;
        }

        & span {
          width: auto;
          height: auto;
          position: relative;
          color: #fff;
          font-weight: 600;
          font-size: 16px;
          text-transform: uppercase;
        }
      }
    }
  }
}