.welcome-section {
  background-image: url("../img/vector.png");
  background-repeat: no-repeat;
  background-position: top right;
  background-size: contain;
  padding-top: 20px;
  padding-bottom: 20px;

  &__banner {
    background-image: linear-gradient(180deg,
        rgba(255, 255, 255, 0.3) 0%,
        rgba(255, 255, 255, 0) 100%),
      url("../img/naklejska-_shirokij-assortiment_.png"),
      url("../img/naklejka-kachestvenno-i-dostupno.png");
    background-position: 0 0, calc(50% + 40px) 180px, calc(50% - 20px) 139px;
    background-repeat: no-repeat;
    backdrop-filter: blur(15px);
    border-radius: 10px;
    width: 94%;
    margin: 0 auto;
    border: 2px solid #fff;
    text-align: center;
    padding-top: 35px;
    padding-bottom: 30px;
  }

  &__title {
    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 55px;
    text-align: center;
    letter-spacing: 0.005em;
    text-transform: uppercase;
    color: #2332b0;
    margin: 0;
  }

  &__text {
    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-align: center;
    padding: 0 25px;
    color: #2332b0;
    margin: 0;
    margin-bottom: 55%;
  }
}

@media (min-width: 768px) {
  .welcome-section {
    background-image: url("../img/star-4.png"), url("../img/vector-(1).png");
    background-position: bottom left, top right;
    background-size: auto, contain;
    padding-bottom: 50px;

    &__banner {
      background-image: linear-gradient(180deg,
          rgba(255, 255, 255, 0.3) 0%,
          rgba(255, 255, 255, 0) 100%),
        url("../img/naklejska-_shirokij-assortiment_1.png"),
        url("../img/naklejka-kachestvenno-i-dostupno-1.png");
      background-position: 0 0, calc(74% + 40px) 554px, calc(-6% - 20px) 568px;
      background-repeat: no-repeat;
      position: relative;
      border-radius: 50px;
      padding-top: 132px;
      padding-left: 100px;
      padding-bottom: 382px;

      &::before {
        position: absolute;
        content: "";
        width: 138px;
        height: 423px;
        background-image: url("../img/star1.png");
        top: 170px;
        left: -30px;
      }

      &::after {
        position: absolute;
        content: "";
        width: 238px;
        height: 221px;
        background-image: url("../img/star-5.png");
        top: -85px;
        left: -30px;
      }
    }

    &__title {
      font-weight: bold;
      font-size: 36px;
      line-height: 55px;
      text-align: left;
      margin-bottom: 40px;
    }

    &__text {
      font-weight: 500;
      font-size: 24px;
      line-height: 30px;
      text-align: left;
      width: 50%;
      margin-bottom: 40px;
    }
  }
}

@media (min-width: 1024px) {
  .welcome-section {
    padding-top: 136px;
    background-image: url("../img/star-4.png"), url("../img/vector-(2).png"),
      url("../img/star-5.png");
    background-position: 200px 38%, right top, -12% 600px;

    &__banner {
      width: 96.5%;
      padding-left: 84px;
      padding-top: 102px;
      padding-bottom: 134px;
      z-index: 10;
      position: relative;
      background-image: linear-gradient(180deg,
          rgba(255, 255, 255, 0.3) 0%,
          rgba(255, 255, 255, 0) 100%),
        url("../img/naklejska-_shirokij-assortiment_1.png"),
        url("../img/naklejka-kachestvenno-i-dostupno22.png");
      background-position: 0 0, calc(83% + 40px) 52px, calc(96% - 20px) 278px;

      &::before {
        position: absolute;
        content: "";
        width: 138px;
        height: 423px;
        background-image: url("../img/star1.png");
        top: -110px;
        left: -30px;
        z-index: -1;
      }

      &::after {
        position: absolute;
        content: "";
        width: 238px;
        height: 221px;
        background-image: none;
        top: 415px;
        left: -116px;
        z-index: 0;
      }
    }

    &__text {
      width: 306px;
    }
  }
}

@media (min-width: 1200px) {
  .welcome-section {
    padding-top: 155px;
    background-image: url("../img/star-4.png"), url("../img/vector1920.png"),
      url("../img/star-5.png");
    background-position: 600px 45%, right top, 300px 550px;

    &__wrapper {
      max-width: 1146px;
      margin: 0 auto;
    }

    &__banner {
      &::before {
        width: 409px;
        height: 455.9px;
        background-image: url("../img/star1desctop.png");
        top: -36px;
        left: -303px;
      }
    }

    &__title {
      margin-bottom: 0;
    }

    &__text {
      margin-bottom: 68px;
    }
  }
}