.popular-queries {
  padding-bottom: 50px;

  &__title {
    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 45px;
    text-align: center;
    color: #28348a;
    margin: 0 auto;
    margin-bottom: 20px;
  }

  &__list {
    @include list-reset;
    display: flex;
    flex-wrap: wrap;
    width: 93.75%;
    margin: 0 auto;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 30px;
  }

  &__item {
    max-width: 50%;
    width: 140px;
    text-align: center;
    min-height: 30px;
    border-radius: 50px;
    margin-bottom: 10px;
    position: relative;
    background: rgba(255, 255, 255, 0.15);
    backdrop-filter: blur(25px);

    & > svg {
      z-index: -1;
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
    }

    & a {
      font-family: Montserrat, sans-serif;
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: 25px;
      text-align: center;
      color: #2230a8;
    }

    &:hover,
    &:focus-within {
      background: rgba(255, 255, 255, 0.75);
    }
  }

  &__color-list {
    @include list-reset;
    display: flex;
    flex-wrap: wrap;
    width: 66.5%;
    margin: 0 auto;
  }

  &__color-item {
    width: 60px;
    height: 60px;
    padding: 10px;
    border-radius: 50%;
    margin: 5px;

    &:hover,
    &:focus-within {
      padding: 8px;
      border: 2px solid white;
    }

    & a {
      display: block;
      width: 100%;
      height: 100%;
      border-radius: 50%;
      background-color: #fff;
    }

    &--black {
      & a {
        background-color: #000;
      }
    }

    &--gray {
      & a {
        background-color: #939393;
      }
    }

    &--red {
      & a {
        background-color: #ff1111;
      }
    }

    &--yellow {
      & a {
        background-color: #fee010;
      }
    }

    &--gold {
      & a {
        background-image: linear-gradient(180deg, #fece87 0%, #d8a460 100%);
      }
    }

    &--blue {
      & a {
        background-color: #0f32ec;
      }
    }

    &--orange {
      & a {
        background-color: #ec7b23;
      }
    }

    &--green {
      & a {
        background-color: #3e9751;
      }
    }

    &--purple {
      & a {
        background-color: #601ca3;
      }
    }

    &--pink {
      & a {
        background-color: #e99ae1;
      }
    }

    &--brown {
      & a {
        background-color: #473434;
      }
    }
  }

  &__color-name {
    display: none;
  }
}

@media (min-width: 768px) {
  .popular-queries {
    &__list {
      justify-content: center;
    }

    &__item {
      margin: 5px;
      min-width: 100px;
      padding: 0 10px;
      width: auto;
      min-height: 30px;
      background-size: cover;
      background-position: right;
    }

    &__color-list {
      width: 100%;
      justify-content: center;
    }

    &__color-item {
      width: auto;
      min-height: 30px;
      height: auto;
      padding: 2px;
      margin: 0;
      margin-right: 5px;
      margin-bottom: 8px;
      color: #fff;

      &--yellow,
      &--gold,
      &--orange,
      &--pink,
      &--white {
        color: #000;
      }

      &:hover,
      &:focus-within {
        padding: 0;
        border: 2px solid white;
        border-radius: 50px;
      }

      & a {
        border-radius: 50px;
        color: inherit;
        width: auto;
        padding: 10px 17px;
        line-height: 10px;
      }
    }

    &__color-name {
      display: inline;
    }
  }
}

@media (min-width: 1024px) {
  .popular-queries {
    &__wrapper {
      width: 750px;
      margin: 0 auto;
    }

    &__title {
      font-weight: 800;
      font-size: 24px;

      &--color {
        display: none;
      }
    }

    &__item {
      padding: 0 15px;
    }
  }

  .catalog-section .popular-queries {
    padding-top: 57px;
    grid-row: 3;
    grid-column: 2/5;

    &__title {
      font-family: Montserrat, sans-serif;
      font-style: normal;
      font-weight: 500;
      font-size: 18px;
      line-height: 25px;
      text-align: left;
      color: #28348a;

      &--color {
        display: block;
        font-size: 14px;
        line-height: 25px;
        position: relative;
        bottom: -47px;
      }
    }

    &__color-list,
    &__list {
      justify-content: flex-start;
    }

    &__color-item {
      &:first-child {
        padding-left: 85px;
      }
    }
  }
}

@media (min-width: 1200px) {
  .popular-queries {
    &__title {
      font-weight: 800;
      font-size: 36px;
      margin-top: 30px;
    }
  }

  .catalog-section .popular-queries {
    padding: 0;
    &__wrapper {
      margin: 0;
    }
    &__list {
      margin: 0;
    }
  }
}
