.header-logo {
    font-weight: 600;
    font-size: 16px;
    align-items: center;
    letter-spacing: 0.05em;
    text-transform: capitalize;
    display: none;

    margin-left: 30px;

    &--visible {
        display: flex;
    }

    &__wrapper {
        width: 38px;
        height: 49px;
    }

    &__text {
        margin-left: 45px;
        font-family: Montserrat, sans-serif;
        font-style: normal;
        font-weight: 600;
        font-size: 14px;
        display: flex;
        align-items: center;
        letter-spacing: 0.05em;
        text-transform: capitalize;
        color: #FFFFFF;
    }
}

@media (min-width: 768px) {
    .header-logo {
        display: flex;
        order: 2;
        margin-left: 0;

        &__wrapper {
            display: none;
            width: 38px;
            height: 49px;
        }

        &--visible {
            & .header-logo__wrapper {
                display: block;
            }
        }
    }

}

@media (min-width: 1024px) {
    .header-logo {

        &__wrapper {
            display: block;
            width: 38px;
            height: 49px;
        }

        &__text {
            margin-left: 15px;
            font-weight: 600;
            font-size: 14px;
            line-height: 45px;
            /* or 321% */

            display: flex;
            align-items: center;
            text-align: center;
            letter-spacing: 0.05em;
            text-transform: capitalize;

            color: #2332B0;
        }
    }

}