.about-us {
  padding-top: 50px;

  &__title {
    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 20px;
    text-align: center;
    color: #333333;
  }

  &__text {
    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 24px;
    color: #333333;
    margin: 0;
    margin-bottom: 20px;
    padding: 10px;
    overflow: hidden;
    height: 270px;

    &--shown {
      height: auto;
    }
  }

  &__text-toggler {
    background-color: transparent;
    border: none;
    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 34px;
    color: #333333;
    text-decoration: underline;
  }

  &__list {
    @include list-reset;
    width: 93.75%;
    margin: 0 auto;
    text-align: center;
  }

  &__item {
    margin-bottom: 30px;
    min-height: 290px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    background-repeat: no-repeat;
    background-position: top center;

    &:nth-child(1) {
      background-image: url("../img/image-26.png");
    }

    &:nth-child(2) {
      background-image: url("../img/image-28.png");
    }

    &:nth-child(3) {
      background-image: url("../img/image-29.png");
    }

    &:nth-child(4) {
      background-image: url("../img/image-30.png");
    }

    &:nth-child(5) {
      background-image: url("../img/image-31.png");
    }
  }

  &__item-title {
    margin: 0;
    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 25px;
    /* or 179% */

    text-align: center;

    /* Gray 1 */

    color: #333333;
  }

  &__item-text {
    margin: 0;
    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 20px;
    /* or 143% */

    text-align: center;

    /* Gray 1 */

    color: #333333;
  }
}

@media (min-width: 768px) {
  .about-us {
    &__title {
      font-family: Montserrat, sans-serif;
      font-style: normal;
      font-weight: 500;
      font-size: 18px;
      line-height: 25px;
      text-align: center;
      color: #333333;
    }

    &__text {
      height: auto;
    }

    &__text-toggler {
      display: none;
    }

    &__list {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-around;
    }

    &__item {
      width: 260px;
      padding-top: 230px;
      display: block;

      &:nth-child(1) {
        background-image: url("../img/image-26.png");
        width: 227px;
      }

      &:nth-child(2) {
        background-image: url("../img/image-28.png");
        width: 243px;
      }

      &:nth-child(3) {
        background-image: url("../img/image-29.png");
        width: 201px;
      }

      &:nth-child(4) {
        background-image: url("../img/image-30.png");
      }

      &:nth-child(5) {
        background-image: url("../img/image-31.png");
      }
    }
  }
}

@media (min-width: 1024px) {
  .about-us {
    &__item {
      margin-right: 10px;
    }
  }
}

@media (min-width: 1200px) {
  .about-us {
    &__wrapper {
      max-width: 1146px;
      margin: 0 auto;
    }

    &__item {
      margin-right: 50px;
    }
  }
}