.description-section {
  &__title {
    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 24px;
    color: #262626;
    display: block;
    margin-bottom: 10px;
  }

  &__text {
    padding: 10px;
    padding-top: 280px;
    background-image: url("../img/rectangle-265.jpg");
    background-repeat: no-repeat;

    &--bottom {
      background-image: url("../img/rectangle-265.jpg");
    }
  }

  &__desktop-br {
    display: none;
  }
}

@media (min-width: 768px) {
  .description-section {
    &__wrapper {
      width: 540px;
      margin: 0 auto;
    }

    &__text {
      background-position: bottom;
      padding: 10px;
      padding-bottom: 280px;
    }

    &__link {
      font-family: Montserrat, sans-serif;
      font-style: normal;
      font-weight: 600;
      font-size: 14px;
      line-height: 25px;
      display: flex;
      align-items: center;
      color: #2230a8;
    }
  }
}

@media (min-width: 1024px) {
  .description-section {
    padding-top: 50px;

    &__wrapper {
      width: 820px;
      margin: 0 auto;
    }

    &__text {
      padding: 0;
      padding-right: 336px;
      background-position: right;
      margin-bottom: 50px;
      background-image: url("../img/rectangle-263.png");
      display: flex;
      flex-direction: column;
      justify-content: center;
      min-height: 230px;

      &--bottom {
        padding: 0;
        padding-left: 420px;
        background-image: url("../img/rectangle-2621.png");
        background-position: left;
        min-height: 400px;
      }
    }

    &__link {
      display: inline;
    }
  }
}

@media (min-width: 1200px) {
  .description-section {
    padding: 0;

    &__desktop-br {
      display: block;
    }

    &__wrapper {
      width: 1146px;
      margin: 0 auto;
    }

    &__text {
      margin: 0;
      padding: 0;
      padding-top: 280px;
      padding-right: 700px;
      padding-bottom: 110px;
      background-position: 50% 0, 100% 130px, 570px 360px;
      margin-bottom: 50px;
      background-image: url("../img/image-32.png"), url("../img/image-33.png"), url("../img/image-34.png");
      display: block;
      min-height: 453px;

      &--bottom {
        padding: 0;
        padding-top: 140px;
        padding-left: 656px;
        background-image: url("../img/rectangle-2622.png");
        background-position: left;
        min-height: 558px;
      }
    }

    &__link {
      display: inline;
      width: 200px;
    }
  }
}