.header-nav {
  display: none;
  padding-left: 40px;
  padding-bottom: 45px;
  overflow: auto;

  svg {
    fill-opacity: 0;
    stroke: #fff;
  }

  &--visible {
    display: block;
  }

  &__list {
    @include list-reset;
  }

  &__item {
    display: flex;
    align-items: center;
    flex-wrap: wrap;



    &:focus-within>svg,
    &:hover>svg {
      stroke: #ffb054;
    }

    &>a {
      margin-left: 24px;
      font-family: Montserrat, sans-serif;
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: 45px;
      display: flex;
      align-items: center;
      letter-spacing: 0.05em;
      text-transform: capitalize;
      color: #ffffff;
      position: relative;
    }

    &--active>a {
      font-weight: 700;
      color: #ffb054;
    }

    &--arrow>a {
      &::after {
        position: absolute;
        content: "";
        width: 10px;
        height: 10px;
        background-image: url("data:image/svg+xml,%3Csvg width='10' height='10' viewBox='0 0 10 10' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M6.90385 4.14251C7.55119 4.53091 7.55119 5.46909 6.90385 5.85749L3.5975 7.8413C2.93098 8.24122 2.08301 7.7611 2.08301 6.98381L2.08301 3.01619C2.08301 2.2389 2.93098 1.75878 3.5975 2.1587L6.90385 4.14251Z' fill='white'/%3E%3C/svg%3E%0A");
        right: -20px;
        top: 17px;
      }
    }

    &--mobile-sublist {
      &>svg {
        stroke: #ffb054;
      }

      &>a {
        font-weight: bold;
        font-size: 14px;
        line-height: 45px;
        display: flex;
        align-items: center;
        letter-spacing: 0.05em;
        text-transform: capitalize;
        color: #ffb054;

        &::after {
          background-image: url("data:image/svg+xml,%3Csvg width='10' height='10' viewBox='0 0 10 10' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M5.85749 6.90483C5.46909 7.55217 4.53091 7.55217 4.14251 6.90483L2.1587 3.59848C1.75878 2.93195 2.2389 2.08398 3.01619 2.08398L6.98381 2.08398C7.7611 2.08398 8.24122 2.93196 7.8413 3.59848L5.85749 6.90483Z' fill='%23FFB054'/%3E%3C/svg%3E%0A");
        }
      }

      & .nav-sublist {
        display: block;
      }
    }
  }
}

@media (min-width: 768px) {
  .header-nav {
    max-width: 374px;
    margin-left: 0;
  }
}

@media (min-width: 1024px) {
  .header-nav {
    max-width: 100%;
    display: block;
    background-color: #2230a8;
    padding: 0 28px;
    overflow: visible;

    &__list {
      display: flex;
      justify-content: space-between;
    }

    &__item {
      & svg {
        display: none;
      }

      &--arrow>a {
        &::after {
          right: -12px;
        }
      }

      &--active>a {
        font-weight: 700;
        color: #fff;
      }

      &--mobile-sublist {
        position: relative;

        & a {
          color: #fff;
        }

        & .nav-sublist {
          border-top: none;
          position: absolute;
          min-width: 100%;
          width: auto;
          padding: 0;
          padding-right: 20px;
          left: 0;
          top: 99%;
          z-index: 10;
          background: linear-gradient(180deg,
              rgba(34, 48, 168, 0.75) 0%,
              rgba(34, 48, 168, 0.75) 100%);
          backdrop-filter: blur(15px);
          /* Note: backdrop-filter has minimal browser support */

          border-radius: 0px 0px 15px 15px;
        }
      }
    }
  }
}

@media (min-width: 1200px) {
  .header-nav {
    overflow: visible;
    max-width: 1146px;
    margin: 0 auto;
    border-radius: 50px;

    &__item {
      &--mobile-sublist {
        position: relative;

        & .nav-sublist {
          border-top: none;
          position: absolute;
          min-width: 100%;
          width: auto;
          padding: 0;
          padding-right: 20px;
          left: 0;
          top: 99%;
          z-index: 10;
          background: linear-gradient(180deg,
              rgba(34, 48, 168, 0.75) 0%,
              rgba(34, 48, 168, 0.75) 100%);
          backdrop-filter: blur(15px);
          /* Note: backdrop-filter has minimal browser support */

          border-radius: 0px 0px 15px 15px;
        }
      }
    }
  }
}