.slider-section {
    width: 100%;
    margin: 0;
    min-height: 215px;

    &__wrapper {
        width: 100%;
        margin: 0;
    }

    &__slider-container {
        width: 100%;
        position: relative;
        min-height: 215px;
        overflow: hidden;
        padding-bottom: 20px;
    }

    &__slider {
        @include list-reset;
        display: flex;
        position: relative;
        width: auto;
    }

    &__slide {
        width: 100%;
        min-width: 100%;
    }

    &__swiper-pagination {
        position: absolute;
        bottom: -16px;
        min-height: 20px;
        display: flex;
        left: 50%;

        transform: translate(-50%);

        & .swiper-pagination-bullet {
            width: 20px;
            height: 4px;
            background-color: #fff;
            border-radius: 5px;

            &:not(:last-child) {
                margin-right: 10px;
            }

            &-active {
                background: #2230A8;
            }

        }
    }
}

@media (min-width: 768px) {
    .slider-section {

        &__slider-container {
            min-height: 320px;
            margin: 0 auto;
        }

        &__slider {
            height: 300px;
        }

        &__slide {
            min-width: 100%;

            & img {
                overflow: hidden;
                border-radius: 50px;
                display: block;
                margin: 0 auto;
                width: 728px;
            }
        }
    }
}


@media (min-width: 1024px) {
    .slider-section {
        &__slider-container {
            min-height: 400px;
        }

        &__slider {
            height: 356px;
            overflow: hidden;
        }

        &__slide {

            & img {
                display: block;
                margin: 0 auto;
                width: 988px;
            }
        }

        &__swiper-pagination {
            bottom: 2px;

            & .swiper-pagination-bullet {
                width: 43px;
                height: 9px;
                background-color: #fff;
                border-radius: 5px;

                &:not(:last-child) {
                    margin-right: 22px;
                }

                &-active {
                    background: #2230A8;
                }

            }
        }
    }
}